/* ========================================================================
  #Font spacing
  ======================================================================== */
 
/**
  * Mixin to set font spacing
  *
  * .example {
  *     @include font-spacing(50);
  * }
  *
  *   Will render as:
  *
  * .example {
  *     letter-spacing: 0.05rem;
  * }
  */

  @mixin font-spacing($val, $is-centered: false) {

    @if type-of($val) == number {
      $space: $val;
  
      @if unitless($val) {
        $space: calc($val / 1000) * 1rem;
      }
  
      @if $is-centered == true {
        margin-left: $space;
      }
  
      font-kerning: normal;
      letter-spacing: $space;
    } @else {
      @warn " #{$val} is not a number value.";
    }
  
  }
  


  