/* ==========================================================================
   #Page Footer
   ========================================================================== */

.ipl-footer {
    background-color: colour(neutral-300);
    margin-top: spacing(lg);
    padding: spacing(xs) 0;
    position: relative;
    z-index: z(footer);

    @include mq($from: tablet) {
      padding: spacing(xl) 0 0;
    }

    //full width required for menu
    .ipl-container {
      margin: 0;
      width: 100%;

      @include mq($from: tablet) {
        margin: 0 spacing(md);
        width: auto;
      }

      @include mq($from: desktop-wide) {
        margin: auto;
      }

    }

}

.ipl-footer-nav__heading {  
  color: colour(white);
  display: flex;
  margin: spacing(sm) spacing(md);

  @include mq($from: tablet) {
    margin: 0 0 spacing(xs);
  }

  //Secondary titles
  &--secondary {
    margin: spacing(md) 0 0 spacing(-md);

    @include mq($from: tablet) {
      margin: spacing(sm) 0 0;
    }
  }
}
  
.ipl-footer-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 0 spacing(md);

    @include mq($from: tablet) {
      flex-direction: row;
      margin: 0;
    }
}

.ipl-footer-nav__level-one {
  border-bottom: 1px solid colour(grey-500 , .75);
  position: relative;

  @include mq($from: tablet) {
    border: none;
    margin-right: spacing(md);
  }
}

.ipl-footer-nav__toggle {
  background: transparent;
  border: 0;
  cursor: pointer;
  height: 2.5rem;
  left: spacing(sm);
  margin: 0;
  padding: 0;
  position: absolute;
  right: spacing(sm);
  top: 5px;
  transform: translateY(0.375rem);
  width: calc(100% - 2rem);

  //Active state
  &[aria-expanded="true"] ~ .ipl-footer-nav__list {
    display: flex;
    flex-direction: column;
    margin: 0 0 spacing(xxs) 0;
    padding: spacing(sm) 0 0 spacing(md);
  }
  &[aria-expanded="true"] .ipl-footer-nav__chevron-down{
    margin-top: 5px;
    transform: translateY(-50%) rotate(180deg);
  }

  &:focus-visible {
    outline: none;

    .ipl-footer-nav__chevron-down {
      color: colour(black);
    }
  }



  @include mq($from: tablet) {
    display: none;
  }

}

.ipl-footer-nav__toggle:focus-visible ~ .ipl-footer-nav__heading {
  color: colour(grey-900);
  position: relative;
  z-index: z(header);
}

.ipl-footer-nav__chevron-down {
  color: colour(white);
  bottom: 0;
  font-size: 1.5rem;
  max-width: 100%;
  position: absolute;
  right: spacing(sm);
  top: spacing(xs);
  transform: translateY(-13%);

  @include mq($from: tablet) {
    display: none;
  }
}


.ipl-footer-nav__list {
  display: none;
    margin: 0 0 spacing(sm) spacing(md);
    padding: 0;

    @include mq($from: tablet) {
      display: block;
      margin: 0;
    }
}

.ipl-footer-nav__list-item {
    display: block;
    margin: 0 0 spacing(xs);
    padding: spacing(xs) 0; 
    transition: $ipl-transition;

    @include mq($from: tablet) {
      display: block;
      padding: spacing(xxxs) 0; 
    }

    &:first-child {
        margin-left: 0;
        padding-left: 0;
    }

    &:last-child {
        margin-right: 0;
        padding-right: 0;
    }

    .ipl-footer-nav__address {
      color: colour(white);
      display: block;
      font-weight: 600;

      &--secondary {
        color: colour(white);
        display: block;
        font-size: .875rem;
      }
    }

}

.ipl-email,
.ipl-social-media {
  position: relative;

  .ipl-icon {
    font-size: 1.5rem;
    margin-right: spacing(xxs);
    position: relative;
    top: 6px;
  }
}

.ipl-footer-nav__link--external {
  padding-right: spacing(xxs);
}

//links
.ipl-footer-nav__link {
    color: colour(grey-100);
    display: inline;
    @include font(primary, regular);
    text-decoration: none;

    &:hover {
      color: colour(white);
      text-decoration: underline;
      text-decoration-thickness: auto;
      text-decoration-thickness: max(1px,.0625rem);
      text-underline-offset: .4em;
    
      @include mq($from: tablet) {
        &.ipl-footer-nav__link--tel:hover {
          text-decoration: none;
         }
       }
    }

    &:focus-visible .ipl-footer-nav__link--title {
      color: colour(black);
    }
    //external links
    &:focus-visible .ipl-footer-nav__link--external::after {
      filter:invert(100%);
    }
}

.ipl-footer-nav__link--tel {

  @include mq($from: tablet) {
    font-weight: bold;
    text-decoration: none;


    &:focus-visible {
      background-color: transparent;
      box-shadow: none;
      color: colour(white);
      outline: none;
      text-decoration: none;
    }
  }

  @include mq($from: desktop) {
    cursor: auto;
    outline: none;
    box-shadow: none;
    border: none;
  }
}

.ipl-footer__bottom {
   display: block;
   margin: spacing(xs) spacing(sm) 0;
   padding: 0;

  @include mq($from: tablet) {
    align-items: center;
    border-top: none;
    display: flex;
    flex-wrap: wrap;
    border-top: 1px solid colour(grey-500);
    justify-content: space-between;
    margin: 0 0 spacing(sm);
  }
}

.ipl-footer__bottom--wrapper {
  align-items: center;
  display: flex;
  flex-wrap: wrap;


  @include mq($from: tablet) {
    flex: auto;
    margin: 0 0 0 auto;
  }

}
.ipl-footer__privacy-link {
  color: colour(white);
  @include rfs(.875rem);
  @include font(primary, regular);
  margin-right: spacing(sm);
  text-decoration: underline;
  text-decoration-thickness: auto;
  text-decoration-thickness: max(1px,.0625rem);
  text-underline-offset: .4em;

  &:hover{
    text-decoration-skip-ink: none;
    text-decoration-thickness: max(3px,.175rem,.12em);
  }
}

.ipl-footer__copyright {
  color: colour(white);
  flex: 0 0 100%;
  @include rfs(.875rem);
  @include font(primary, regular); 
  margin: spacing(sm) 0;

  @include mq($from: tablet) {
    margin: 0;
    text-align: center;
  }
}




  .ipl-footer__iso {
    display: block;
    margin: spacing(xs) 0;
    padding: 0;
    width: 6rem;

    &:focus-visible .ipl-logo--iso {
      color: colour(black);
    }

    @include mq($from: tablet) {
      margin: spacing(xs) 0 0;
      padding: 0;
      width: 8rem;
    }

    .ipl-logo--iso {
      color:colour(grey-200);
      font-size: 6rem;
      height: 3rem;
  
      &:hover {
        color:colour(white);
        filter: brightness(150%);
      }
      
  
      @include mq($from: tablet) {
        font-size: 8rem;
        height: 4rem;
      }
  
    }

  }




// External links 
// base64 format used as background image
.ipl-footer-nav__link--external {

  &::after {
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 576 512'%3E%3Cpath fill='%23FFFFFF' d='M576 24v127.984c0 21.461-25.96 31.98-40.971 16.971l-35.707-35.709-243.523 243.523c-9.373 9.373-24.568 9.373-33.941 0l-22.627-22.627c-9.373-9.373-9.373-24.569 0-33.941L442.756 76.676l-35.703-35.705C391.982 25.9 402.656 0 424.024 0H552c13.255 0 24 10.745 24 24zM407.029 270.794l-16 16A23.999 23.999 0 00384 303.765V448H64V128h264a24.003 24.003 0 0016.97-7.029l16-16C376.089 89.851 365.381 64 344 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h352c26.51 0 48-21.49 48-48V287.764c0-21.382-25.852-32.09-40.971-16.97z'/%3E%3C/svg%3E");
    display: inline-flex;
    left: spacing(xxxs);
    position: relative;
    top: 1px;
    width: 1rem;
  }

  &:focus-visible::after {
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 576 512'%3E%3Cpath fill='%23000' d='M576 24v127.984c0 21.461-25.96 31.98-40.971 16.971l-35.707-35.709-243.523 243.523c-9.373 9.373-24.568 9.373-33.941 0l-22.627-22.627c-9.373-9.373-9.373-24.569 0-33.941L442.756 76.676l-35.703-35.705C391.982 25.9 402.656 0 424.024 0H552c13.255 0 24 10.745 24 24zM407.029 270.794l-16 16A23.999 23.999 0 00384 303.765V448H64V128h264a24.003 24.003 0 0016.97-7.029l16-16C376.089 89.851 365.381 64 344 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h352c26.51 0 48-21.49 48-48V287.764c0-21.382-25.852-32.09-40.971-16.97z'/%3E%3C/svg%3E");
    }


}










