/* ==========================================================================
   #Callouts
   ========================================================================== */

/**
  * Default stylized boxes for featured content. Often positioned to notify
  * the user about significant information.
  *
  * Reference: https://design.oit.ncsu.edu/docs/nc-state-shortcodes/callout/
  *
  * Border options :65 for _note, _highlight, _important. e.g.
  *
  *   <div class="ipl-callout ipl-callout__note"></div>
  *
  *   Will render as:
  *
  *   .ipl-callout {
  *       border-left: .375rem solid colour(grey-500);
  *       margin: 1.125rem 0;
  *       padding: 1.125rem;
  *    }
  *
  *   .ipl-callout__note {
  *       background-color: #f4f4f4;
  *       border-left-color: #005987;
  *    }
  *
  */

  .ipl-callout {
    background-color: transparent;
    border-left: .1875rem solid colour(grey-500);
    padding: spacing(lg) spacing(md);
    margin: spacing(lg) 0 spacing(md);

    @include mq($from: tablet) {
      margin-bottom: spacing(lg);
    }

  }

  .ipl-callout__title {
    margin-top: 0;
    margin-bottom: spacing(md);
    margin-left: spacing(lg);
  }

  .ipl-callout__sub-title {
    font-weight: normal;
    margin-left: spacing(lg);
    margin-top: spacing(lg);
  }

  .ipl-callout__text {
    margin-top: spacing(md);
    margin-bottom: spacing(xs);
    margin-left: spacing(sm);


    &:last-child {
      margin-bottom: 0;
    }

  }

  .ipl-callout__link {
    line-height: 1.5;
    margin: spacing(xs) spacing(xs) spacing(sm) spacing(lg);
  }
  
  .ipl-callout__code {
    border-radius: spacing(xs);
  }

  .ipl-callout + .ipl-callout {
    margin-top: -spacing(xs);
  }


// Mixin
@mixin ipl-callout($background, $border-color) {
  background-color: $background;
  border-left-color: $border-color;
}




// Variations
.ipl-callout__note { 
  @include ipl-callout(colour(grey-100), colour(blue-200)); 
}

.ipl-callout__highlight { 
  @include ipl-callout(colour(grey-100), color(code-class)); 
}

.ipl-callout__important { 
  @include ipl-callout(colour(grey-100), colour(error)); 
}