/* ==========================================================================
   #Spacings
   ========================================================================== */


/* Specific spacing values for elements. Spacing base values are in 
 * ./settings/global.scss
 *
 * Reference: https://css-tricks.com/magic-numbers-in-css/
 *
 * // Sapcing is divided into two sections, HTML values and CSS values
 *
 * // HTML values
 *
 *   E.g.
 *   .ipl-m-sm {}     //margin small
 *   .ipl-pl-lg {}    //padding-left large
 *   .ipl-mr-xxs {}   //margin-right xx-small
 *   .ipl-pr-none {}  //padding-right
 *   .ipl-px-lg {}    //padding-left & padding-right lg
 *   .ipl-my-small {} //margin-top & margin bottom small
 *
 *  
 *
 *   <div class="ipl-px-lg"></div>
 *
 *   Will render as:
 *
 *   .ipl-pt-lg {
 *       padding-left:  1.25em;
 *       padding-right: 1.25em;
 *    }
 *  
 *
 * // CSS Values:
 *
 *  .example {
 *     padding: spacing(xs) spacing(-lg) 0 spacing(md);
 *   }
 *    
 *   Will render as:
 *
 *   .example {
 *     padding: .5rem -1.25rem 0 1.0rem;
 *    }
 *   
 */

 //Generate HTML values:
 $ipl-spacing-properties: (
  "p": "padding",
  "m": "margin",
) !default;

 $ipl-spacing-sides: (
  "" : "",               //classes that set margin or padding all sides
  "t": "-top",           //classes that set margin-top or padding-top
  "r": "-right",         //classes that set margin-right or padding-right
  "b": "-bottom",        //classes that set margin-bottom or padding-bottom
  "l": "-left",          //classes that set margin-left or padding-left
  "x": "-left" "-right", //classes that set both *-left and *-right
  "y": "-top" "-bottom", //classes that set both *-top and *-bottom
) !default;

$ipl-spacing-sizes: (
  '-xxxs'   :   $spacing-01,  //4px
  '-xxs'    :   $spacing-02,  //8px
  '-xs'     :   $spacing-03,  //12px
  '-sm'     :   $spacing-04,  //16px
  '-md'     :   $spacing-05,  //20px
  '-lg'     :   $spacing-06,  //24px
  '-xl'     :   $spacing-07,  //32px
  '-xxl'    :   $spacing-08,  //48px
  '-xxxl'   :   $spacing-09,  //64px
  '-xxxxl'  :   $spacing-10,  //80px
  "-none"   :   0,            //0
  '-auto'   :   auto          //auto 
) !default;

@each $property-namespace, $property in $ipl-spacing-properties {

  @each $direction-namespace, $direction-rules in $ipl-spacing-sides {

    @each $size-namespace, $size in $ipl-spacing-sizes {

      .ipl-#{$property-namespace}#{$direction-namespace}#{$size-namespace} {

        @each $direction in $direction-rules {
          #{$property}#{$direction}: $size !important;
        }

      }

    }

  }

}


 //Generate CSS values:
 $spacing: (
  'xxxs'   :   $spacing-01,  //4px
  'xxs'    :   $spacing-02,  //8px
  'xs'     :   $spacing-03,  //12px
  'sm'     :   $spacing-04,  //16px
  'md'     :   $spacing-05,  //20px
  'lg'     :   $spacing-06,  //24px
  'xl'     :   $spacing-07,  //32px
  'xxl'    :   $spacing-08,  //48px
  'xxxl'   :   $spacing-09,  //64px
  'xxxxl'  :   $spacing-10,  //80px
  'none'   :   0,
  'auto'   :   auto,

  //Negative Margin values
  '-xxxs'   :   -$spacing-01,  //-4px
  '-xxs'    :   -$spacing-02,  //-8px
  '-xs'     :   -$spacing-03,  //-12px
  '-sm'     :   -$spacing-04,  //-16px
  '-md'     :   -$spacing-05,  //-20px
  '-lg'     :   -$spacing-06,  //-24px
  '-xl'     :   -$spacing-07,  //-32px
  '-xxl'    :   -$spacing-08,  //-48px
  '-xxxl'   :   -$spacing-09,  //-64px
  '-xxxxl'  :   -$spacing-10,  //-80px
) !default;


@function spacing($name,  $schema:$spacing) {
  @if map-has-key($schema, $name) {
      @return map-get($schema, $name);
  } @else {
      @warn "The value #{$name} is not in the map '$spacing--map'";
      @return null;
  }
};

