/* ==========================================================================
   #Chartered Accountants
   ========================================================================== */

  /**
   * Elements specific to the Chartered Accoutnants landign page 
   *  
   *
   */

// main content CA_ANZ negative margin value
// due to banner curvature
.main-content {
    margin-top: -6rem;
    position: relative;

    @include mq($from: tablet) {
        margin-top: spacing(-xxxl);
      }
   }
  
   .ipl-intro {
    display: flex;
    margin: 0 auto;
    justify-content: center;
   }

   .ipl-intro__image {
    border: 1px solid transparent;
    border-radius: radius(xs);
    bottom: 0;
    left: -1rem;
    margin: 1rem auto;
    max-width: 350px;
    padding: 1rem;
    position: relative;

    &:hover {
      bottom: spacing(xxxs);
      border-color: colour(grey-200);
      box-shadow: shadow(sm);
    }
  }

  p {
    @include mq($from: tablet) {
      max-width: 65ch;
    }
   
  }

  //form
  .ipl-form--wrapper {
    margin-bottom: spacing(xxl);

    @include mq($from: tablet) {
      margin-bottom: spacing(xl);
    }
  }

  .ipl-form {
    background-clip: padding-box;
    background-color: colour(white);
    border: 1px solid colour(grey-300);
    border-radius: 1rem;
    box-shadow: shadow(sm);
    display: block;
    height: auto;
    margin: spacing(md) auto spacing(md);
    padding: spacing(lg);
    position: relative;
    width: 100%;

    @include mq($from: tablet) {
      margin: spacing(xl) 0 0 auto;
      padding: spacing(xl);
      width: 90%; 
    }
  }

  .ipl-iframe-wrapper {
    border: none;
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0;
    padding: 0 0 spacing(xl);
    width: 100%;
  }

.ipl-iframe {
  border: none;
  flex-grow: 1;
  height: 100vh;
  margin: 0 0 spacing(lg);
  padding: 0;
  width: 100%;
}


.ipl-ca-form--fullname,
.ipl-ca-form--size-region {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
 

  @include mq($from: desktop) {
  column-gap: spacing(md);
   flex-direction: row;
  }

}

.ipl-ca-form span input,
.ipl-ca-form div span input,
.ipl-ca-form span select,
.ipl-ca-form--message {
  background-color: transparent;
  border-radius: spacing(xxs);
  outline: 0;
  border: 2px solid colour(grey-400);
  box-shadow: inset 0 2px 3px rgba(0,0,0,.15);
  font-size: 1.1rem;
  margin-top: spacing(xxs);
  padding: spacing(xxs) spacing(xs);
  transition: outline ease-in-out .15s;

  &:focus-visible {
    background-color: transparent!important;
    border-color: transparent;
    -webkit-box-decoration-break: clone;
    box-decoration-break: clone;
    box-shadow: inset 0 2px 3px rgba(0,0,0,.15)!important;
    color: #0b0c0c;
    outline: 3px solid #fd0!important;
    text-decoration: none;
    outline-offset: -2px!important;
  }
}



.ipl-ca-form--first-name,
.ipl-ca-form--last-name,
.ipl-ca-form--email,
.ipl-ca-form--business,
.ipl-ca-form--size,
.ipl-ca-form--region,
.ipl-ca-form--phone,
.ipl-ca-form--message {
  display: flex;
  flex-direction: column;
  margin-bottom: spacing(md);
}

.ipl-ca-form--size,
.ipl-ca-form--region {
  max-width: 50%;

  @include mq($from: tablet) {
    max-width: 100%;
    width:75%;
     }

  @include mq($from: desktop) {
   width: 50%;
    }
}

.ipl-ca-form--phone {
  max-width: 220px;
}



.ipl-ca-form--message {
  width: 100%;

}