/* ==========================================================================
   #Primary navigation
   ========================================================================== */

 /**
  *  Default navigation, desktop and mobile - keyboard accessible.
  *  Current link referenced via 'aria current' line :350
  * 
  */


// Mobile Navigation and logo wrapper 
.ipl-header__menu-logo-wrapper {
  align-content: center;
  align-items: center;
  display: flex;

  .ipl-divider--vertical-light {
    margin-right: spacing(sm);

    @include mq($from: tablet-wide) {
      display: none;
    }
  }
}

// Mobile Navigation Button 
.ipl-header__mobile-button {
  display: flex;
  align-self: center;
  justify-content: flex-start;

  @include mq($from: tablet-wide) {
    display: none;
  }

}

//Close mobile navigation
.ipl-nav__mobile-toggle {
  align-self: center;
  background-color: transparent;
  border: 1px solid transparent;
  color: colour(grey-900);
  cursor: pointer;
  height: spacing(xl);
  outline: none;
  padding: spacing(md);
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: spacing(xl);
  z-index: z(dropdown);

  .ipl-nav__menu {
    bottom: 0;
    font-size: 1.75rem;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
  }

  &:focus:not(:hover),
  &:focus-within:not(:hover),
  &:focus-visible {
    box-shadow: inset 0 0 0 3px colour(link-visited);
    outline: 3px solid colour(focus);
    outline-offset: 0;
    border-color: colour(link-visited);
    border-width: 1px;
  }

    //toggle menu button state
    use.false {
      display: none;
    }
    &[aria-expanded="true"] .ipl-nav__menu {
     
      use.true {
        display: none;
      }
      use.false {
        display: block;
      }
  
    }
    
  @include mq($from: tablet-wide) {
    display: none;
  }

}

//Mobile overlay
.ipl-nav__mobile-overlay {
  background: colour(black, 90%);
  height: 100%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  transition: all .15s ease;
  width: 100%;
  z-index: z(footer);

  &.active {
   opacity: 1;
   pointer-events: auto;

   & > .ipl-header {
      padding-top: 0.875rem;
      padding-bottom: 0.875rem;
    }
  }

  @include mq($from: tablet-wide) {
    display: none;
    opacity: 0;
    z-index: z(negative);
  }

}

//Mobile menu header
.ipl-nav__mobile-header{
  display: flex;
  margin: 0 0 0 spacing(md);
  padding: .85rem 0;
 
  @include mq($from: tablet-wide) {
    display: none;
  }

  .ipl-divider--vertical {
    margin: 0 spacing(sm);
    max-height: 1.9rem;
  }

}

// Primary navigation
.ipl-nav {
  background-color: colour(white);
  box-shadow: 0 0 4px 2px rgb(0 0 0 / 40%);
  height: 100vh;
  left: 0;
  overflow-y: auto;
  position: fixed;
  top: 0;
  transform: translateX(-282px);
  transform-style: preserve-3d;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-property: transform;
  visibility: hidden;
  width: 272px;
  will-change: transform;
  z-index: z(dropdown);

  &.active {
    overflow-y: auto;
    overflow-x: clip;
    transform: translateX(0);
    visibility: visible;
    
    //show ~ hide overlay
    & ~ .ipl-nav__mobile-overlay {
      display: block;
    }
    //unset colour values when mobile menu open
    .ipl-nav__menu {
      color: colour(grey-900);
      font-size: 1.75rem;
    }
    //remove filter when header background is not set
    .ipl-header__logotype {
      filter: unset;
    }
  }

  @include mq($from: mobile-wide) {
    overflow-y: unset;
  }

  @include mq($from: mobile, $and: '(orientation: landscape)') {
    overflow-y: scroll;
  }

  @include mq($from: tablet-wide) {
    background-color: transparent;
    box-shadow: none;
    display: flex;
    height: auto;
    overflow-y: unset;
    position: relative;
    transform: translateX(0);
    visibility: visible;
    width: 100%;
    z-index: z(header);
  }

  //remove margin for mobile menu
  .ipl-container {
    height: 100vh;
    margin: 0;
    width:100%;

    @include mq($from: tablet-wide) {
      height: auto;
      margin-right: spacing(lg);
      margin-left: spacing(lg);
    }

    @include mq($from: desktop) {
      height: auto;
      margin: auto;
    }

  }

}

.ipl-nav__list {
  letter-spacing: .02em;
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;

  @include mq($from: tablet-wide) {
    border-bottom: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

}

.ipl-nav__list-item  {
  border-bottom: none;
  border-top: 1px solid colour(grey-300);
  cursor: pointer;
  padding: spacing(sm) 0;  
  position: relative;

  @include mq($from: tablet-wide) {
    align-items: center;
    border-top: none;
    margin: 0 spacing(md) 0;
    padding: 0;  

    &:first-of-type {
      margin-left: 0;
    }
  }


 
  &.active {
    padding-bottom: 0;

      //rotate chevron
      .ipl-nav__chevron-down {
        top: 20px;
        transform: rotate(180deg);
      }
  }

  //expand secondary navigation in mobile
  &.active > .ipl-nav__level-two {
    display: block;
    
    @include mq($from: tablet) {
      margin-left: 0;
    }
  
  }

  @include mq($from: tablet-wide) {
    border-top: none;
    display: flex;
    text-align: left;
    

    &.active {
      padding-bottom: spacing(sm);
    }

    &:hover > .ipl-nav-link:after,
    &.js-focus > .ipl-nav-link:after {
      background-color: colour(neutral-300);
      left: 0;

      @include mq($from: tablet-wide) {
        left: 12px;
      }
    }

    &:hover > .ipl-nav__level-two,
    &.js-focus > .ipl-nav__level-two {
        visibility: visible;   
    }

    //Rotate arrow on hover
    &:hover > .ipl-nav-expand__wrapper{
      transform: scaleY(-1);
  }

    &.js-focus .ipl-nav-expand__wrapper .ipl-nav-expand__btn:focus::after {
      top: 4px;
      transform: rotate(225deg);
       
    } 

  }

}

//Primary navigation
.ipl-nav__level-one {
  border-bottom: 1px solid colour(grey-300);

  @include mq($from: tablet-wide) {
    border-bottom: none;

    //Current link
    > .ipl-nav__list-item:hover {
      box-shadow: inset 0px -4px 0px 0px rgba(33,33,33,1);
    }
  }

} 

//Navigation links
.ipl-nav-link {
  display: inline;
  color: colour(grey-900);
  font-size: rfs(15px);
  font-weight: lighter;
  letter-spacing: .02em;
  line-height: 1.67;
  margin: spacing(md);
  text-decoration: underline;
  text-decoration-color: transparent;
  -moz-text-decoration-color: transparent;
  transition-delay: .4s;
  transition-property: text-decoration-color;

  //If no arrow for mega-menu arrow 
  &.ipl-nav-link--no-children {
    margin-right: 0;
  }

  &:hover {
    text-decoration: underline;
    text-decoration-thickness: auto;
    text-decoration-thickness: max(3px,.1875rem,.12em);
    text-underline-offset: .3em;
  }

  @include mq($from: tablet-wide) {
      margin: spacing(xs) 0;
      
      &:active, 
      &:hover {
          outline: none;
          text-decoration: none;
          text-decoration-color: transparent;
      }

  &[aria-current]::after {
    box-shadow: inset 0 -6px 0 0 colour(neutral-300);
    left: 0;
    position: absolute;
  }

  }

}
//Links & Desktop illustration placement
.ipl-nav__list-link {
  color: colour(grey-900);
  display: flex;
  flex-wrap: wrap;
  font-size: rfs(16px);
  position: relative;
  text-decoration: none;

  .ipl-nav__list-title:hover {
    text-underline-offset: 0.3em;
    text-decoration-thickness: max(3px, 0.1875rem, 0.12em);
    text-decoration: underline;
  } 

  @include mq($from: tablet-wide) {
    margin: 0 spacing(sm) spacing(sm);
    text-decoration: none;



    //Underline from hovering over all block
    &:hover .ipl-nav__list-title {
      text-underline-offset: 0.3em;
      text-decoration-thickness: max(3px, 0.1875rem, 0.12em);
      text-decoration: underline;
      -webkit-text-decoration-skip-ink: none;
      text-decoration-skip-ink: none;
      -webkit-text-decoration-skip: none;
      text-decoration-skip: none;
    }


  }

  // Desktop menu card titles
  .ipl-nav__list-title {
    color: colour(grey-900);
    font-weight: lighter;
    text-underline-offset: 0.2em;

    @include mq($from: tablet-wide) {
      font-size: 1rem;
      font-weight: 600;
      text-decoration: underline;
      text-underline-offset: .3em;
      text-decoration-thickness: max(1px, .0625rem);
  
      &:hover {
        text-decoration-thickness: max(3px, 0.175rem, 0.12em);
        -webkit-text-decoration-skip-ink: none;
        text-decoration-skip-ink: none;
        -webkit-text-decoration-skip: none;
        text-decoration-skip: none;
      } 

    }
 
  }


}

//Dropdown menu
.ipl-nav__level-two {
  background-color: colour(grey-200);
  border-top: 1px solid colour(grey-300);
  box-shadow: inset 0px 10px 5px -10px rgba(0,0,0,0.5),inset 0px -10px 5px -10px rgba(0,0,0,0.5);
  display: none;
  list-style-type: none;
  margin: spacing(sm) 0 0;
  padding: 0 spacing(md);
  width: 100%;
  
  .ipl-nav__list-item {
    border-top: none;

    @include mq($from: tablet-wide) {
      margin: spacing(sm) 0 spacing(xs);
      padding: 0;
      width: 33%;
    }
  }

  //Desktop
  @include mq($from: tablet-wide) {
    border-top: none;
    box-shadow: inset 0 10px 4px -10px rgba(0,0,0,.5),0 2px 6px 1px rgba(0,0,0,.25);
    clip-path: inset(0px -5px -8px -5px);
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    left: 0;
    margin: 0;
    padding: spacing(xl) spacing(lg) spacing(xxl);
    position: fixed;
    top: 100%;
    transition: all 0s ease;
    transition-delay: 0.5s;
    visibility: hidden;
    z-index: z(header);
  }


  //Desktop menu
  .ipl-nav__list-content {
    flex: 0 0 75%;
    margin: 0;

    @include mq($from: tablet-wide) { 
      margin-left: spacing(sm);
    }

  }

  .ipl-nav__list-image {
    display: none;
    
    @include mq($from: tablet-wide) {
      align-self: center;
      display: block;
      height: 4rem;
      width: 4rem;
    }

  }

  .ipl-nav__list-data {
    display: none;

    @include mq($from: tablet-wide) {
      color: colour(grey-900);
      display: block;
      text-decoration: none;
      margin-top: spacing(xs);
      font-size: 1rem;
      font-weight: lighter;
     }

  }


  .ipl-nav__footer {
    display: none;

    @include mq($from: tablet-wide) {
      display: flex;
      margin: spacing(md) spacing(sm) 0 spacing(md);
      width: 100%;

    .ipl-nav__view--all {
      color: colour(grey-900);
      display: flex;
      font-size: 1.05rem;
      position: relative;
      text-decoration: underline;
      text-underline-offset: .2em;
      text-decoration-thickness: max(1px, .0625rem);

      &:hover {
        text-decoration-thickness: max(3px, 0.175rem, 0.12em);
        -webkit-text-decoration-skip-ink: none;
        text-decoration-skip-ink: none;
        -webkit-text-decoration-skip: none;
        text-decoration-skip: none;
      } 

      &:after {
        border-bottom: 2px solid colour(grey-900);
        border-right: 2px solid colour(grey-900);
        content: "";
        height: 10px;
        right: -14px;
        position: absolute;
        top: 9px;
        transform: rotate(-45deg);
        width: 10px;
       }

     }
    }

  }

}
//Dropdown arrow
.ipl-nav__toggle {
  background: transparent;
  border: 0;
  box-shadow: inset 7px 0 0 -6px colour(grey-500);
  cursor: pointer;
  height: 2.5rem;
  margin: 0;
  padding: 0;
  position: absolute;
  right: spacing(xxs);
  top: -2px;
  transform: translateY(spacing(xs));
  width: 2.5rem;

  &::after {
    border-bottom: 2px solid colour(grey-900);
    border-right: 2px solid colour(grey-900);
    bottom: 0;
    content: "";
    height: 10px;
    left: 3px;
    margin: auto;
    position: absolute;
    right: -2px;
    top: -4px;
    transform: rotate(45deg);
    width: 10px;
  }

   &[aria-expanded="true"]::after {
    transform: rotate(-135deg) translate(-20%, -20%);
  }

  @include mq($from: tablet-wide) {
    display: none;
  }

}

//Secondary dropdown menu
.ipl-nav__level-two-link {
  border-bottom: 2px solid transparent;
  margin: spacing(md) spacing(sm);
  position: relative;
  text-align: left;

  &:not(:focus) {
    border-bottom-color: transparent;
    color: colour(link-primary);
  }

  @include mq($from: tablet-wide) {
    display: flex;
    color: colour(neutral-300);
    text-align: left;
    margin: spacing(sm) 0;

    &:active, 
    &:not(:focus):hover {
      color: colour(link-primary-hover);
    }

  }

}

//Desktop dropdown menu keyboard access
.ipl-nav-expand__wrapper {
  display: none;

  @include mq($from: tablet-wide) {
    border: none;
    display: block;
    margin: 0;
    padding: 0;
    position: relative;
    width: 0;
  }

}

.ipl-nav-expand__btn {

  @include mq($from: tablet-wide) {
    clip: auto;
    background: transparent;
    border: none;
    bottom: 0;
    height: 16px;
    margin: auto 0;
    outline: none;
    overflow: visible;
    padding: 2px;
    position: absolute;
    right: 0;
    top: -2px;
    transition: right .25s ease-in-out;
    width: 16px;

    &::after {
      content: "";
      border-right: 2px solid colour(black);
      border-bottom: 2px solid colour(black);
      height: 10px;
      left: 0;
      margin: auto;
      position: absolute;
      right: 0;
      top: 0;
      transform: rotate(45deg);
      width: 10px;
    }
    
    &:focus {
        background: colour(white);
        border: none;
        clip: auto;
        height: 16px;
        margin: 0;
        overflow: visible;
        position: absolute;
        outline: 1px solid colour(neutral-300);
        padding: 2px;
        right: 0;
        top: -8px;
        width: 16px;



    }
  }
}


