/* ==========================================================================
   #Feature elements
   ========================================================================== */

/** 
* Highlighted features 
*
* Currently supported by product illustrations 
*/

.ipl-features {
    margin-top: spacing(xxl);
  }

.ipl-feature-list {
    border-spacing: 0; 
    display: flex;  
    flex-wrap: wrap; 
    list-style: none;
    margin: 0; 
    padding: 0;
    position: relative;
  }
    
  //Card
  .ipl-feature {
    background-clip: padding-box; 
    border: 0 solid transparent; 
    border-width: 0 20px 0 0; 
    display: flex;
    margin: 0 0 spacing(sm);
    padding: 0;
    position: relative;
    width: calc(100% / 1);
  
    @include mq($from: tablet) {
      border: 0 solid transparent; 
      border-width: 0 40px 20px 0; 
      padding: spacing(xs) spacing(md) 0 0;
      width: calc(100% / 2);
      }

  }
  
  
  //Image
  .ipl-feature--image {
    display: flex;
    justify-content: center;
    margin: auto;
    position: relative;
  }
  
  .ipl-feature--image > * {
    display: block;
    max-width: 100%;
  }
  
  
  //Title
  .ipl-feature--title {
    @include font(primary, bold);
    margin: 0 0 spacing(xs);
  }
  
  
  //Content
  .ipl-feature--content {
    flex-grow: 1;
    padding: 0;  
  }
  
  .ipl-feature--centre-align {
    text-align: center;
  } 
  
  .ipl-feature--text {
    color: colour(neutral-300);
    display: block;
    font-size: rfs(1rem);
    font-weight: normal;
    line-height: 1.5;
    margin: 0 0 spacing(sm);
    max-width: 65ch;
  }
  
  .ipl-feature--link {
    border-bottom: none;
    box-shadow: inset 0 0 0 1px colour(grey-500);
    color: colour(link-primary);
    display: flex;
    font-size: rfs(1.25rem);
    height: 100%;
    padding: spacing(sm);
    text-decoration: none;
    width: 100%;
  
    &:hover:not(:focus) {
      border: none;
      box-shadow: inset 0 0 0 3px colour(neutral-300);
      color: colour(link-primary-hover);
      outline: none;
    }
  
    //Full card selection
      &:after {
        bottom: 0; 
        content:'';
        left:0;
        position: absolute;
        right:0;
        top: 0;
      }
    
  }