/* ==========================================================================
   #Blockquote
   ========================================================================== */

/**
  * Default styling for inline-quotes and quotes
  *
  * Reference: 
  * https://css-tricks.com/snippets/css/simple-and-nice-blockquote-styling/
  *
  */

  @mixin ipl-blockquote($ipl-quote: "\2014") {
    background-color: colour(grey-700);
    border-left: 10px solid colour(neutral-300);
    @include font(blockquote, bold);
    @include rfs(1.25rem);
    margin: spacing(md) 0;
    padding: spacing(md);
    quotes: "\201C""\201D""\2018""\2019";
    text-indent: $ipl-quote-width;

      &::before {
        color: colour(neutral-300);
        content: open-quote;
        @include rfs(4rem);
        line-height: .1em;
        margin-right: .15em;
        vertical-align: -.5em;
      }
  
     p {
      display: inline;
  
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  
    + figcaption {
  
      &::before {
        content: $ipl-quote;
      }

    }
  
  }
  
// Blockquotes
blockquote {
  color: colour(neutral-300);
  @include ipl-blockquote();
  border-top-left-radius:(sm);
  border-top-right-radius:(sm);
}


// Inline quotes.
q {
  quotes: "\2018" "\2019" "#{$ipl-open-quote}" "#{$ipl-close-quote}";

  &::before {
    content: "\2018";
    content: open-quote;
  }

  &::after {
    content: "\2019";
    content: close-quote;
  }

  q {

    &::before {
      content: "\201c";
      content: open-quote;
    }

    &::after {
      content: "\201d";
      content: close-quote;
    }
  }
}