/* ==========================================================================
   #Divider
   ========================================================================== */

/**
 * The divider gives style control to the horizontal rule to provide a visual 
 * break and divide content eg:
 *
 *  <hr class="ipl-divider" aria-hidden="true">
 *
 *
 *  A divider can also be added to the base of the element:
 *
 *  <div class="ipl-add-divider"></div>
 *
 *
 *  A light version can be set by adding --light to the class:
 *
 *  <hr class="ipl-divider--light"></hr>
 *
 */

//Divider base styles
.ipl-divider,
.ipl-divider--light {
  @include horizontal-rule(colour(neutral-300), solid, 2px);
  height: 1px;
  margin-right: spacing(sm);
  margin-left: spacing(sm);
  max-width: 90em;

  @include mq($from: tablet) {
    margin-right: spacing(lg);
    margin-left: spacing(lg);
   }
 
   @include mq($from: desktop-wide) {
     margin-right: auto;
     margin-left:  auto;
   }
}

.ipl-divider--light {
  color: colour(grey-300);
}


//Divider vertical
.ipl-divider--vertical, 
.ipl-divider--vertical-light {
  border-left: 1px solid colour(neutral-300);
  height: 2.5rem;
  margin: 0 auto;
}

.ipl-divider--vertical-light {
  border-left-color: colour(grey-300);
}



//Divider applied on existing element
.ipl-add-divider ,
.ipl-add-divider--light {
  border-bottom-color: colour(neutral-300);
  border-bottom-width: 1px;
  border-bottom-style: solid; 
  margin-bottom: spacing(lg);
}

.ipl-add-divider--light {
  border-bottom-color: colour(grey-300);
}
