/* ==========================================================================
   #Card
   ========================================================================== */

/**
 * The card object gives control over the vertical alignments of the text and 
 * image.
 *
 *
 * 1. Forces equal cell heights,
 * 2. Creates gaps
 * 3. prevents background bleed.
 * 4. number of cards per row
 */


 // Prevents irregular margin behaviour in Chrome 
.ipl-card--wrapper {
  overflow: hidden;
  margin-bottom: spacing(sm);
}
 
.ipl-card-list {
  border-spacing: 0; 
  list-style: none;
  margin: 0 0 0 spacing(xs); 
  padding: 0;
  display: flex;  /* [1] */
  flex-wrap: wrap; /* [1] */
  position: relative;

  @include mq($from: desktop) {
    margin: 0;
  }
}
  
//Card
.ipl-card {
  background-clip: padding-box; /* [3] */
  border: 0 solid transparent; /* [2] */
  border-width: 0 20px 20px 0; /* [2] */
  margin: 1px 0 0;
  padding: spacing(xs) 0 0;
  position: relative;
  width: calc(100% / 2); /* [4] */

  @include mq($from: mobile-wide) {
    width: calc(100% / 3);
  }

  @include mq($from: phablet) {
    width: calc(100% / 4);
  }

  @include mq($from: desktop) {
    width: calc(100% / 5); 
  }

}


//Image
.ipl-card--image {
  background-color: colour(white);
  border-radius: radius(xxs);
  display: flex;
  justify-content: center;
  padding: spacing(sm) 0;
  position: relative;
  opacity: 1;
}

.ipl-card--image > * {
  display: block;
  width: 75%;
}


//Title
.ipl-card--title {
  @include font(primary, bold);
  font-size: rfs(1.25rem);
  margin: spacing(sm) 0 spacing(xs);
}


//Content
.ipl-card--content {
  flex-grow: 1;
  justify-content: space-between;
  margin-top: spacing(sm);
  padding: 0;

  & > *:nth-last-child(2) {
    margin-top: spacing(xs);
    margin-bottom: auto;
  } 

}

.ipl-card--centre-align {
  text-align: center;
} 

.ipl-card--text {
  color: colour(neutral-300);
  font-size: rfs(.875rem);
  font-weight: normal;
  line-height: 1.5;
  margin: spacing(md) 0 spacing(lg);
  max-width: 65ch;
}

.ipl-card--link {
  background-color: colour(white);
  border: 1px solid colour(grey-300);
  border-radius: spacing(xs);
  border-radius: max(0px, min(8px, calc((100vw - 4px - 100%) * 9999))) / 8px;
  box-shadow: shadow(xs);
  color: colour(link-primary);
  display: flex;
  font-size: rfs(1.25rem);
  height: 100%;
  text-decoration: none;
  transition: all .1s ease-out;
  width: 100%;
  height: 100%;

  @include mq($from: desktop) {
    
    &:hover:not(:focus) {
      border-color: colour(grey-200);
      box-shadow: shadow(lg);
      color: colour(link-primary-hover);
      outline: none;
      transform: scale(1);
    } 
  }

 

  //Full card selection
    &:after {
      bottom: 0; 
      content:'';
      left:0;
      position: absolute;
      right:0;
      top: 0;
    }
  
}

