/* ==========================================================================
   #Page utilities
   ========================================================================== */

   /**
    * Page utilities is placed at the base of the content prior to the footer
    * to provide options for publish date, return to top, print etc.
    *
    */

  .ipl-utilities {
    border-top: 2px solid colour(grey-500);
    display: flex;
    flex-direction: column;
    margin-top: spacing(lg);
    padding-top: 0;
    width: 100%;

    @include mq($from: tablet-small) {
      flex-direction: row;
    }

  }

  .ipl-utilities__last-modified {
    display: inline-flex;
    font-family: inherit;
    font-size: 0;
    justify-content: flex-start;
    margin-bottom: 0;
    padding: 0;
    width: 100%;

    @include mq($from: tablet-small) {
      margin: 0;
    }
  }

  .ipl-utilities__elements {
    display: flex;
    justify-content: flex-start;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;

    @include mq($from: tablet-small) {
      justify-content: flex-end;
    }
  }

  .ipl-print__button {
    background-color: transparent;
    border: none;
    border-bottom: 2px solid transparent;
    color:colour(neutral-300);
    cursor: pointer;
    display: flex;
    font-weight: 400;
    height: 1.5rem;
    margin-top: 1px;
    padding: 0 0 .125vh spacing(xl);
    position: relative;
    text-decoration: none;
    width: auto;

    &:before {
      height: 1.25rem;
      left: -.025rem;
      top: 1px;
      position: absolute;
      content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='20' height='20' viewBox='0 0 24 22.2'%3E%3Cpath fill='%232A2A2A' d='M23.2 10c-.5-.5-1.2-.8-2-.8h-.9V5.5c0-.4-.1-.8-.3-1.3-.2-.5-.4-.8-.7-1.1L17.1 1c-.3-.3-.6-.5-1.1-.7-.4-.2-.8-.3-1.2-.3H5.1c-.4 0-.7.1-1 .4-.3.3-.4.6-.4 1v7.8h-.9c-.8 0-1.4.3-2 .8-.5.6-.8 1.2-.8 2v6c0 .1 0 .2.1.3.1.1.2.1.3.1h3.2v2.3c0 .4.1.7.4 1 .3.3.6.4 1 .4h13.8c.4 0 .7-.1 1-.4.3-.3.4-.6.4-1v-2.3h3.2c.1 0 .2 0 .3-.1.3-.1.3-.2.3-.3v-6c0-.8-.3-1.4-.8-2zm-4.7 10.3h-13v-3.7h12.9v3.7zm0-9.2h-13V1.8h9.2v2.3c0 .4.1.7.4 1 .3.3.6.4 1 .4h2.3l.1 5.6zm3.4 1.5c-.2.2-.4.3-.6.3-.3 0-.5-.1-.6-.3-.2-.2-.3-.4-.3-.6 0-.3.1-.5.3-.6.2-.2.4-.3.6-.3.2 0 .5.1.6.3.2.2.3.4.3.6s-.1.5-.3.6z'/%3E%3C/svg%3E");
      display: inline-flex;
      width: 1.25rem;

    //Windows High contrast
    @media (-ms-high-contrast: active) and (forced-colors: active) {
      filter: invert(0%) sepia(120%) saturate(5000%) hue-rotate(50deg) brightness(815%) contrast(125%);
    }

    }

    &:hover {
      border-bottom-color: colour(neutral-300);
      text-decoration: none;
      padding-bottom: .125vh;
    }

  }

  .ipl-utilities__btn--label {
    align-items: center;
    display: flex;
    font-size: 0;

    &:focus-visible {
      outline: 0;
    }
  }