/* ==========================================================================
   #Shared
   ========================================================================== */

/**
 * Shared declarations for certain elements. Always declare margins in the same 
 * direction:
 *
 * Reference: https://csswizardry.com/2012/06/single-direction-margin-declarations/
 */



address,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, ol, ul,
figure,
hr,
table,
fieldset {
  margin-bottom: spacing(sm);
}



//Consistent indentation for lists.
dd, ol, ul {
  margin-left: spacing(sm);
}


