/* ========================================================================
   #Typography
   ======================================================================== */

/**
 * Global typography & font-optimization. Add additional locally hosted 
 * fonts from line :45 
 * 
 * 
 * Define font standards
 *
 * [1] Modern Browsers
 * [2] Safari, Android, iOS
 * [3] Safari, Android, iOS
 */


// Path to the fonts folder, relative to the compiled stylesheet
// @type String

@mixin font($directory, $family, $name, $style: normal, $weight: normal) {
@font-face {
    font-family: $family;
    src:
    url('#{$directory}/#{$family}/#{$name}/#{$name}.woff') format('woff'), //  [1] 
    url('#{$directory}/#{$family}/#{$name}/#{$name}.ttf') format('truetype'),//[2] 
    url('#{$directory}/#{$family}/#{$name}/#{$name}.svg') format('svg');  //   [3] 
    font-style: $style;
    font-weight: $weight;
 }
}


// Locally hosted fonts directory
$font-directory: "../fonts"; 



// Fonts from directory by:          Family,                            Name,       Style,    Weight

// Ubuntu
   
    @include font($font-directory,    'Ubuntu-Regular',                  'black'     normal,   400 );
    @include font($font-directory,    'Ubuntu-Italic',                   'black'     normal,   400 );
    @include font($font-directory,    'Ubuntu-Medium',                   'black'     normal,   600 );
    @include font($font-directory,    'Ubuntu-MediumItalic',             'black'     normal,   600 );
    @include font($font-directory,    'Ubuntu-Bold',                     'black'     normal,   700 );
    @include font($font-directory,    'Ubuntu-BoldItalic',               'black'     normal,   700 );

// Open sans

    @include font($font-directory,    'OpenSans-Regular',                'black'     normal,   400 );
    @include font($font-directory,    'OpenSans-Italic',                 'black'     normal,   400 );
    @include font($font-directory,    'OpenSans-Semibold',               'black'     normal,   600 );
    @include font($font-directory,    'OpenSans-SemiboldItalic',         'black'     normal,   600 );
    @include font($font-directory,    'OpenSans-Bold',                   'black'     normal,   700 );
    @include font($font-directory,    'OpenSans-BoldItalic',             'black'     normal,   700 );



// Font shorthand through abstracted labels
@mixin font($fontLabel, $weightLabel) {
    $fonts: (body: (stack: 'Ubuntu', weights:(regular: 400, medium: 600, bold: 700) )) !default;
    $fontWeight: 400;
    $fontStyle: normal;
    $fontStack: 'None';

    // Match given type with fontstacks
    @each $fontKey, $font in $fonts {
        @if $fontLabel == $fontKey {
            // get stack and weights
            @each $elem, $value in $font {
                @if $elem == 'stack' {
                    $fontStack: $value;
                }

                @if $elem == 'weights' {
                    @each $weightKey, $weight in $value {
                        @if $weightKey == $weightLabel {
                            $fontWeight: $weight;
                        }
                    }
                }
            }
        }
    }

    // set CSS
    font-family: $fontStack;
    font-weight: $fontWeight;
}





