/* ========================================================================
   #banner
   ======================================================================== */

/**
 * When using images for Background-image @mixin is imported, file naming 
 * format ends with -sm, -md, -lg include image name and file type, i.e.
 *
 * File name: image-sm.jpg, image-md.jpg, image-lg.jpg
 *
 * .example {
 *   @include background-image("image-name", jpg);
 * }
 *
 */



//Import banner mixin 
@import "../tools/hero-block";



//Banner 
//Background image
.ipl-banner,
.ipl-banner--ipl,
.ipl-banner--cpl {
  @include gradient(neutral);
  display: flex;
  margin: 0;
  padding: 0;
  position: relative;
  z-index: z(base);
}
.ipl-banner--ipl {@include gradient(orange);}
.ipl-banner--cpl {@include gradient(blue);}

.ipl-banner--curve {
  bottom: -2px;
  height: 250px;
  overflow: hidden;
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: z(module);

  @include mq($from: desktop-wide) {
    height: 325px;
  }

  
  &::after {
    content: "";
    width: 300%;
    height: 0;
    padding-top: 200%;
    border-radius: 100%;
    background: #fff;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateX(-50%);

    @include mq($from: tablet) {
      top: 65%;
      width: 330%;
     }

     @include mq($from: tablet-wide) {
      top: 60%;
      width: 320%;
     }

    @include mq($from: desktop-wide) {
     top: 70%;
     width: 370%;
    }
  }
}

//Magic numbers used for static image placement
.ipl-banner--client {
  right: -5%;
  margin-right: auto;
  margin-left: auto;
  opacity: .25;
  position: relative;
  top: spacing(xl);
  z-index: z(base);

  @include mq($from: mobile-wide) {
    left: spacing(-xxxxl);
    top: spacing(sm);
  }

  @include mq($from: tablet) {
    right: 10%;
    top: 0;
  }

  @include mq($from: tablet-wide) {
    opacity: .5;
  }
    
  &:after {
      background-image: url("data:image/svg+xml;base64,");
    background-position: center 0;
    background-repeat: no-repeat;
    background-size: cover;
    content: "";
    height: 700px;
    position: absolute;
    width: 710px;
  }
}

.ipl-banner {
    align-items: center;
    display: flex;
    padding-bottom: spacing(xxs);
    padding-top: spacing(xl);
    position: relative;
}



//Overlay to meet contrast ratio standards for title, synopsis and buttons
.ipl-banner__overlay {
  background-color: colour(black);
  opacity: 0.3;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: z(base);
}

// Banner content
.ipl-banner__content {
  align-content: center;
  display: flex;
  padding: 1rem;
  margin-top: 6rem;
  position: relative;
  z-index: z(footer);
}

.ipl-banner__description {
  margin: 0;

  @include mq($from: phablet) {
    margin: 0 10%;
  }
  
  @include mq($from: tablet) {
    margin: 0 0 0 5vw;
  }
  @include mq($from: desktop) {
    margin: 10vh 0 0 5vw;
  }
}



.ipl-tagline,
.ipl-tagline--white {
  align-items: flex-start;
  color: colour(grey-900);
  display: flex;
  flex-direction: column;
  margin: 0 0 spacing(md);

  &--white {
    color: colour(white);
  }
}


.ipl-banner__subtitle {
  margin-bottom: 0;
}

.ipl-banner__synopsis {
  color: colour(white);
  @include font(secondary, regular);
  @include rfs(16px);
  margin-bottom: spacing(xl);

  @include mq($from: phablet) {
    @include rfs(18px);
    max-width: 45ch;
  }

}


.ipl-banner__visual--wrapper {
  bottom: 2.25rem;
  display: flex;
  left: spacing(sm);
  position: relative;

  @include mq($from: tablet) {
    bottom: 1.75rem;
    left: auto;
  }

}


.ipl-banner__visual {
  bottom: 0;
  left: 0;
  margin: spacing(md) auto;
  max-width: 65%;
  position: relative;
  right: 0;
  top: 0;

  @include mq($from: tablet) {
    margin: auto;
    max-width: 75%;
  }

  @include mq($from: desktop-wide) {
    max-width: 100%;
  }


} 
