/* ========================================================================
   #Fonts
   ======================================================================== */

/**
 * Style the basic fontstack. Add additional fonts accordingly 
 * ./tools/typography.scss
 *
 * .example {
 *    @include font(primary, regular);
 *  }
 *
 */

 // Fonts
$fonts: (
  //Primary
  primary: (stack: ('Ubuntu', 'Tahoma', sans-serif), weights: (regular: 400, medium: 600, bold: 700)),

  //Secondary
  secondary: (stack: ('Open Sans', 'Arial', sans-serif), weights: (regular: 400, medium: 600, bold: 700)),

  //Serif
  serif: (stack: (Merriweather, Georgia, 'Times New Roman', serif), weights: (regular: 400, medium: 500, bold: 700)),

  //Blockquote
  blockquote: (stack: (Georgia, Times, Times New Roman, serif), weights: (regular: 400, medium: 500, bold: 700)),

  //Mono
  mono: (stack: ('Source Code Pro', Courier, mono), weights: (regular: 400, medium: 500, bold: 700)),
  ) !default;


