/* ==========================================================================
   #Page Header
   ========================================================================== */

.ipl-header-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: z(overlay); 

    &.sticky {
      position: fixed;
      top: -100px;
      transition: all .2s;
    }
  
    &.show{
      top: 0;
    }
}

.ipl-header {
  background-color: colour(white);
  border: 1px solid colour(grey-400);
  box-shadow: shadow(sm); 
  margin: spacing(sm) auto 0;
  max-width: 47rem;
  padding: 0 spacing(sm);
  position: relative;
  border-radius: radius(pill);
  width: 100%;


}
//increase depth on scroll
.ipl-header-wrapper.sticky.show .ipl-header {
  box-shadow: 0 5px 10px 0 rgba(0,0,0,.3);
}

.ipl-header__wrapper {
  display: flex;

  .ipl-divider--vertical {
    align-self: center;
    border-color: colour(grey-400);
    display: flex;
    margin: 0 spacing(sm) 0 spacing(xxs);
    max-height: 1.9rem;

    @include mq($from: tablet-wide) {
      display: none;
   }
  }
}

//Header wrapper - desktop & mobile
.ipl-logo__wrapper {
  margin: spacing(sm) 0;
}

.ipl-logo--primary,
.ipl-mobile__logo-wrapper {
  margin: 0;
  text-decoration: none;

  //Adjust divider between hamburder menu & logo
  .ipl-divider--vertical-light {
    height: 1.9rem;
    margin-left: spacing(sm);
    margin-right: spacing(sm);

    @include mq($from: tablet-wide) {
      border: none;
      margin: 0;
   }
  }
}

.ipl-logo--primary {
  display: inline;
  margin: auto;
}


//Logo settings
.ipl-header__logotype {
  align-items: center;
  border-bottom: none;
  display: flex;
  margin: 0;
  text-decoration: none;

  //CloudPayroll logo
.ipl-logo {
  height: 1.5rem;
  margin: 0 spacing(xxs) 0 0;
  transition: all .25 ease-in-out;
  width: 1.5rem;

  @include mq($from: mobile) {
    margin: 0;
  }

  @include mq($from: desktop) {
    height: 2rem;
    width: 2rem;
  }
}
.ipl-logo--title {
  align-items: end;
  bottom: -5px;
  color: colour(neutral-300);
  display: none;
  @include font(primary, bold);
  font-size: 1.25rem;
  margin-left: spacing(xs);
  position: relative;

  @include mq($from: mobile) {
    bottom: -4px;
    display: flex;
    margin-left: spacing(xs);
  }

  sup {
    font-size: 0.5rem;
    position: relative;
    top: -8px;
  }

}
.cpl-logo--blue-500{
 display: none;
}

  &:focus {
    background-color: colour(focus);
    box-shadow: 0 -2px colour(focus), 0 4px colour(link-visited);
    color: colour(link-visited);
    filter: unset;
    outline: 3px solid transparent;
    text-decoration: none;

    .ipl-logo, .ipl-logo--title {
      filter: brightness(100) invert(1);
    }
  }

  &:focus-visible {

    .ipl-header__logo { 
      background-color: colour(focus);
      color: colour(link-visited);
      box-shadow: 0 -2px colour(focus), 0 4px colour(link-visited);
      outline: 3px solid transparent;
      padding: 0;
      text-decoration: none;
  
      .ipl-header__logo--image {
        filter: invert(1) brightness(0);
      }
    }
  }

  @include mq($from: tablet-wide) {
    box-shadow: none;
    margin: 0 spacing(xs);
    padding: 0;
  }  

}

//Right hand buttons
.ipl-header__actions {
  align-items: center;
  display: flex;
  margin: 0 0 0 auto;
  position: relative;
  z-index: z(base);
}
