/* ==========================================================================
   #Illustrations
   ========================================================================== */

 /**
  *  Localised directory for illustrations
  *  background-image used for decorative images not requiring alt""
  *
  */


//Highlighted Features illustrations
.ipl-feature--image {
    background-repeat: no-repeat;
    filter: drop-shadow(2px 3px 2px rgba(0,0,0,.2));
    font-size: 4rem;
    height: 1em;
    margin-top: spacing(xxs);
    margin-right: spacing(sm);
    max-width: 100%;
    width: 1em;

    @include mq($from: tablet) {
      font-size: 4.5rem;

    }

