/* ==========================================================================
   #Skip link
   ========================================================================== */

 /**
  * The skip (navigation) link provides a way for users of Assistive technology
  * to bypass large or repetitive blocks of content.
  *
  */

  .skip-link {
    background-color: colour(white);
    color: colour(neutral-300);
    font-size: rfs(18px);
    @include font(primary, regular);
    padding: spacing(xs);
    position: absolute;
    left: -100%;
    text-decoration: none;
    top: -100%;
    width:auto;
    padding: spacing(xxs);
    z-index: z(error); 

    &:active,
    &:focus-visible {
      left: 0;
      margin: spacing(xxs);
      top: 0;
    }
  }

  //Target hidden in mobile
  .skip-link__desktop {
    display: none;

    @include mq($from: tablet) {
      display: block;
    }

  }
  
  .skip-link:not([tabindex="-1"]) {
    outline: .2rem solid transparent;
    outline-offset: spacing(sm);
  }
  


