/* ==========================================================================
   #Buttons
   ========================================================================== */
 
/**
  * 1. Allow user to style box model properties.
  * 2. Line different sized buttons up a little nicer.
  * 3. Make buttons inherit font styles (often necessary when styling `input`s as
  *    buttons).
  * 4. Reset/normalize some styles.
  * 5. Force all button-styled elements to appear clickable.
  *
  * Colour shorthand
  * .example {
  *    @include button-variant(colour(blue-200), white); //background-color, color
  * }
  *
  *
  * Will render as:
  *
  * .example {
  *   background-color: #00A0DF;
  *   color: #FFF;
  *  } 
  *
  *  Adding the variation light or dark will add :hover to the button, e.g.
  *
  * .example {
  *    @include button-variant(colour(blue-200), white, lighten); 
  *  }
  *
  */

  //Import button mixin 
  @import "../tools/buttons";

 /**
  * [1] Button Settings
  *
  * [2] Button Size
  * 
  * [3] Button Colours
  *
  * [4] Pill shape settings
  *
  * [5] Transitions from button to icon in mobile view
  *
  * [6] Drop shadow
  *
  */
  
  /* Settings [1] */
  .ipl-btn,
  .ipl-btn--primary,
  .ipl-btn--primary-frame,
  .ipl-btn--secondary,
  .ipl-btn--secondary-frame,
  .ipl-btn--tertiary,
  .ipl-btn--tertiary-frame  {
    align-items: center;
    appearance: none;
    border-color: transparent;
    background-image: none;
    border-radius: radius(xxs);
    border: 2px solid;
    cursor: pointer; 
    display: inline-flex;
    flex: 0 0 auto;
    @include font(primary, normal);
    font-size: rfs(1rem);
    justify-content: space-between;
    margin: spacing(xs) spacing(md) spacing(xs) 0; 
    overflow: hidden;
    padding: spacing(xs) spacing(sm);
    position: relative;
    text-align: center;
    text-decoration: none;
    touch-action: manipulation;
    user-select: none;
    vertical-align: middle; /* [2] */
    text-overflow: ellipsis;
    transition: $ipl-transition;
    transition: background-color 160ms, 
                color 160ms, 
                box-shadow 160ms, 
                border 160ms;
  
    // Truncate overflow
    white-space: nowrap;


    //First button
    &:first-of-type { margin-left: 0;}
  
    //Last button 
    &:last-of-type {
      margin-right: 0;
    }

}



/* Button Size [2] */
.ipl-btn--sm {
  font-size: rfs(.875rem);
  padding: spacing(xxxs) spacing(xs);
}

//Large
.ipl-btn--lg {
  font-size: rfs(1.25rem);
  padding: spacing(sm) spacing(md);
}

.ipl-btn--full-width {
  width: 100%;
}


/* Button Colours [3] */
  //Primary
  .ipl-btn--primary {
    @include button-variant(colour(blue-200), white, lighten); 
  }
  //Primary frame only
  .ipl-btn--primary-frame {
    @include button-variant(transparent, colour(blue-200)); 
    border: 2px solid colour(blue-200);
  
    &:hover:not(:focus){
      background-color: colour(blue-200);
      color: colour(white)
    }
  }
  

//Secondary
  .ipl-btn--secondary {
    @include button-variant(colour(orange-200), white, darken); 
  }
  //Secondary frame only
  .ipl-btn--secondary-frame {
    @include button-variant(transparent, colour(orange-200)); 
    border: 2px solid colour(orange-200);
  
    &:hover:not(:focus){
      background-color: colour(orange-200);
      color: colour(white)
    }
  }
  

  //Tertiary
  .ipl-btn--tertiary {
    @include button-variant(colour(neutral-300), white, 'darken');
  }
  
  .ipl-btn--tertiary-frame {
    @include button-variant(transparent, colour(neutral-300)); 
    border: 2px solid colour(neutral-300);
  
    &:hover:not(:focus){
      background-color: colour(neutral-300);
      color: colour(white);
    }
  }
  

  //Disabled
  .ipl-btn--disabled {
    @include button-variant(transparent, colour(grey-400)); 
    border: 2px solid colour(grey-400);
  
    &:hover:not(:focus){
      background-color: transparent;
      color: colour(grey-400);
    }
  }
  
  
/* Pill shape [4] */
  .ipl-btn--rounded {
    border-radius: radius(pill);
  }


  /* Transitions from button to icon in mobile view [5] */
  .ipl-btn--label {
    background-color: transparent;
    border: none;
    color: colour(neutral-300);
    display: flex;
    align-items: center;
    font-size: rfs(.875rem);
    justify-content: flex-start;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    text-decoration: none;

    &-text {
      display: none;
    
      @include mq($from: mobile) {
        display: flex;
      }
    
    }
    &-icon {
      border-radius: 50%;
      color: colour(neutral-300);
      display: flex;
      font-size: 2rem;
    
      &.ipl-white {
        color: colour(white);
      }
    
      @include mq($from: mobile) {
        display: none;
      }
    }

    &:active {
      background: transparent;
    }
  
    @include mq($from: mobile) {
      @include button-variant(colour(neutral-300), white, 'darken');
      border: 2px solid colour(neutral-300);
      font-size: rfs(.875rem);
      padding: spacing(xxxs) spacing(sm);
    }
  
   }
  




  /* Drop shadow [6] */
  .ipl-btn--shadow {
    box-shadow: shadow(xs);
  }

  //Buttons icon placement
  .ipl-btn__icon,
  .ipl-btn__icon--left {
    display: flex;
    font-size: rfs(1.5rem);
    margin: 0 0 0 spacing(sm);

    &--left{
      margin: 0 spacing(sm) 0 0;
    }
  }
  

  
