/* ==========================================================================
   #Container
   ========================================================================== */

/**
 * Page-level constraining and container elements.
 */

 $ipl-container-width:   80em !default; //1280px
 $ipl-container-padding: spacing(md) !default;

 @if (type-of($ipl-container-width) != number) {
   @error "`#{$ipl-container-width}` needs to be a number.";
 }
 
 .ipl-container {
   margin-right: spacing(sm);
   margin-left: spacing(sm);
   transition: $ipl-transition;
   width: calc(100% - spacing(sm)*2);

   @include mq($from: tablet) {
    margin-right: spacing(lg);
    margin-left: spacing(lg);
    max-width: $ipl-container-width;
    width: calc(100% - spacing(lg)*2);
   }

   @include mq($from: desktop-wide) {
     margin-right: auto;
     margin-left:  auto;
     width: 100%;
   }

 }
 



//Full-width column containers

//Wrap .ipl-container into column-containers for full width-options
.ipl-column-container {
  padding: spacing(lg) 0;

  @include mq($from: tablet) {
    padding: spacing(xxxl) 0;
  } 

}

