/** 
* Global variables, site-wide settings, config switches.
*
* Config...........Project-level configuration and feature switches.
* Core.............Sass core and setup settings.
* Global...........Project-wide variables and settings. 
*/
/* ==========================================================================
   Config
   ========================================================================== */
/* ========================================================================
   #Global
   ======================================================================== */
/**
 * The global settings file contains any project-wide variables; elements 
 * that need to be made available to the entire codebase.
 * 
