/* ========================================================================
   #Settings
   ======================================================================== */
/**
 * The settings file establishes key setup variables. They set how the SCSS
 * framework functions, only modify and preconfigure with caution. 
 *
 * refer to setttings/global.scss for site wide variables & shorthand rules
 *
 * 
 */
/**
* Mixins or functions that implement either logic (e.g. conditionally 
* outputting CSS) or calculations, and that need to be made available globally.
* This layer should not output any CSS.
*
* Border...........Defines border width, colour and position
* Border-radius....Provides one-line method for setting border-radius
* Button...........Mixin for setting Primary, Secondary and Outline buttons
* Clearfix.........Mixin to use clearfix as a selector.
* Colour...........Automatically calculates and updates colour tokens
* Columns..........Styling for creating paragraph columns
* Device pixels....Media query for retina images
* Elevation........Mixin for shadow settings 
* Font-spacing.......Sets paragraph font spacing
* Hero-block.......Sets banner path/image size for desktop, tablet & mobile
* Horizontal-rule..Horizontal rules to extend HR element
* Links............Mixin to generate states for visited, hover, active and focus
* Media-queries....Default media query manager based on Sass Mq.
* Placeholder......Mixin to set placeholder style in -moz, -ms, webkit
* Responsive-Font-size..A mixin to automatically calculate font-size based on display width
* Spacings.........Nudge bits of the DOM around with these spacing classes
* Sprites..........Raw sprites rendered through the Mustache template                     
* Typography.......Global typography mixin & font optimization.
* Z-index..........Map naming convention for consistant z-index settings
*/
/* ==========================================================================
   #Border
   ========================================================================== */
/*
 * A border that is generated as classes. Defines width, colour and 
 * position.
 *
 * .example {
 *    @include border(1px solid, color(blue), bottom right);
 * }
 *
 */
/* ========================================================================
  #Border Radius
  ======================================================================== */
/**
  *
  * Provides a concise function for setting the border-radius size:
  *
  * .example {
  *    border-radius: radius(xl);
  *  }
  *
  *  Will render as:
  *
  * .example {
  *    border-radius: radius(xl);
  *  }
  *
  */
/* ==========================================================================
   #Buttons
   ========================================================================== */
/*
 * Buttons with Primary, secondary, tertiary and outline mixins.
 * Options for dark or light variations on hover.
 *
 * .example {
 *    @include button-variant(black, white); 
 * }
 *
 * Will render as:
 *
 * .example {
 *   background-color: #000;
 *   color: #FFF;
 *  } 
 *
 */
/* ==========================================================================
   #Clearfix
   ========================================================================== */
/**
 * Mixin to use clearfix as a selector. 
 * 
 *
 * Reference: https://www.cssmojo.com/the-very-latest-clearfix-reloaded/
 *
 * .example {
 *   @include ipl-clearfix();
 * }
 *
 */
/* ========================================================================
  #Colours
  ======================================================================== */
/**
* The colour function automatically calculates and updates css shorthand
* - edit colours at elements/colour-tokens.scss
* 
* .example {
*   background-color: colour(blue-200);
*   color: colour(white, .95);
* }
*
*  will render as:
*
* .example {
*   background-color: #005688;
*   color: #ffffff;
*   opacity: .95;
* }
*
*/
/* ========================================================================
  #Colour tokens
  ======================================================================== */
/**
*  Global representation for the given colour, where colour(colour-name) is 
*  the friendly name for the colour based on rgba;
* 
* .example {
*   background-color: colour(blue-200);
*   color: colour(white, .95);
* }
* 
* When using CSS colour variables with SASS embed the colour
* within brackets: var(--variablename); e.g.
*
* .example {
*   --calendar: colour(yellow-100);
* }
*
*
*/
/* ==========================================================================
   #Column count
   ========================================================================== */
/**
  * Default styling for creating paragraph columns, if no column-rule required
  * use 0. 
  *
  * .ipl-column-count {
  *    @include column-split(2, 5%, 3px solid colour(neutral-300)); 
  *  }
  *
  * Will render as:
  *
  * .ipl-column-count {
  *   column-count: 2;
  *   column-gap: 5%;
  *   column-rule: 3px solid #d3d3d3;
  * }
  *
  */
/* ==========================================================================
   #Device pixels
   ========================================================================== */
/**
  * Media query for retina images (device-pixel-ratio)
  * 
  *
  * Providing a @2x image for screens that support it:
  *
  * .example {
  *   @include ipl-device-pixel-ratio {
  *     background-image: ipl-image-url("my-image-2x.png");
  *   }
  *
  * Using a custom ratio
  *
  * .example {
  *   @include ipl-device-pixel-ratio(3) {
  *     background-image: ipl-image-url("my-image-3x.png");
  *   }
  *
  */
/* ========================================================================
   #Elevation
   ======================================================================== */
/**
 * Sass map naming convention for consistant shadow settings
 * 
 * .example {
 *     box-shadow: shadow(sm); 
 *  }
 *
 */
/* ========================================================================
  #Font spacing
  ======================================================================== */
/**
  * Mixin to set font spacing
  *
  * .example {
  *     @include font-spacing(50);
  * }
  *
  *   Will render as:
  *
  * .example {
  *     letter-spacing: 0.05rem;
  * }
  */
/* ========================================================================
   #Gradients
   ======================================================================== */
/**
*
* To use the gradient background colour function, pass in colour preferences 
* the gradient is set 30deg though can be changed on :57 
*
* .example {
*   @include gradient(blue);
*  }
*
*  Will render as:
*
* .example {
*   background: linear-gradient(30deg, colour(blue-300), colour(blue-100));
*  }
*
*/
/* ========================================================================
   #Banner
   ======================================================================== */
/**
 *  
 * Set the banner/banner path, extension and responsive images for desktop, 
 * tablet and mobile. Set images accordingly ../components/banner.scss
 *
 */
/* ========================================================================
   #Horizontal-rules
   ======================================================================== */
/**
 * Horizontal rules to extend HR - further settings at 
 * _./components/divider.scss
 * 
 * Reference: https://css-tricks.com/simple-styles-for-horizontal-rules/
 *
 * <hr class="example">
 *
 * .example {
 *    @include horizontal-rule(color(neutral-200), dotted, 3px); 
 *  }
 *
 */
/* ========================================================================
   #Links
   ======================================================================== */
/*
 * The link mixin generates states for visited and hover, focus has an 
 * individual state for outlines.
 *
 *  To adjust colours: ./elements/link-colours.scss
 */
/* ==========================================================================
   #Media queries
   ========================================================================== */
/**
 * Mixin from Sass Mq - https://sass-mq.github.io/sass-mq/
 * Further docs @ https://github.com/sass-mq/sass-mq
 * 
 * mq() takes up to three optional parameters:
 *
 * $from: inclusive min-width boundary
 * $until: exclusive max-width boundary
 * $and: additional custom directives
 *
 * $until as a keyword is a hard limit i.e. it's breakpoint - 1.
 *
 * .example {
 *    @include mq($from: mobile) {
 *       color: red;
 *    }
 *    @include mq($until: tablet) {
 *      color: blue;
 *    }
 *    @include mq(mobile, mobile-wide) {
 *     color: green;
 *    }
 *    @include mq($from: tablet, $and: '(orientation: landscape)') {
 *      color: teal;
 *    }
 *    @include mq(40.000em) {
 *      color: hotpink;
 *    }
 *    @include mq(tablet, $media-type: screen) {
 *      color: hotpink;
 *    }
 * }
 *
 *  values and names are on :90 and can be changed to suit:
 *    $mq-breakpoints: (
 *    mobile:        22.500em, //360px
 *    mobile-wide:   30.000em, //480px
 *    phablet:       35.000em, //560px
 *    tablet-small:  40.000em, //640px
 *    tablet:        48.000em, //768px
 *    tablet-wide:   64.000em, //1024px
 *    desktop:       78.000em, //1248px
 *    desktop-wide:  90.000em  //1440px
 *    ) !default;
 *
 */
/* ==========================================================================
   #Placeholder
   ========================================================================== */
/**
 *
 * Set placeholder style in all supporting browser formats, -moz, -ms,-webkit
 *
 * input,
 * textarea {
 *    @include input-placeholder {
 *      color: colour(grey);
 *    }
 * }
 *
 */
/* ========================================================================
   #Responsive Font size
   ======================================================================== */
/**
 * The font-size settings 'rfs' (responsive font size) automatically calculate and 
 * update the font-size
 * based on the dimensions of the browser viewport. 
 *
 * Reference: https://css-tricks.com/using-a-mixin-to-take-the-math-out-of-rfs-sizes/
 *
 * .example {
 *   @include font-size(16px);
 * }
 *
 */
/* ==========================================================================
   #Spacings
   ========================================================================== */
/* Specific spacing values for elements. Spacing base values are in 
 * ./settings/global.scss
 *
 * Reference: https://css-tricks.com/magic-numbers-in-css/
 *
 * // Sapcing is divided into two sections, HTML values and CSS values
 *
 * // HTML values
 *
 *   E.g.
 *   .ipl-m-sm {}     //margin small
 *   .ipl-pl-lg {}    //padding-left large
 *   .ipl-mr-xxs {}   //margin-right xx-small
 *   .ipl-pr-none {}  //padding-right
 *   .ipl-px-lg {}    //padding-left & padding-right lg
 *   .ipl-my-small {} //margin-top & margin bottom small
 *
 *  
 *
 *   <div class="ipl-px-lg"></div>
 *
 *   Will render as:
 *
 *   .ipl-pt-lg {
 *       padding-left:  1.25em;
 *       padding-right: 1.25em;
 *    }
 *  
 *
 * // CSS Values:
 *
 *  .example {
 *     padding: spacing(xs) spacing(-lg) 0 spacing(md);
 *   }
 *    
 *   Will render as:
 *
 *   .example {
 *     padding: .5rem -1.25rem 0 1.0rem;
 *    }
 *   
 */
