/* ========================================================================
   #banner
   ======================================================================== */
/**
 * When using images for Background-image @mixin is imported, file naming 
 * format ends with -sm, -md, -lg include image name and file type, i.e.
 *
 * File name: image-sm.jpg, image-md.jpg, image-lg.jpg
 *
 * .example {
 *   @include background-image("image-name", jpg);
 * }
 *
 */
/* ========================================================================
   #Banner
   ======================================================================== */
/**
 *  
 * Set the banner/banner path, extension and responsive images for desktop, 
 * tablet and mobile. Set images accordingly ../components/banner.scss
 *
 */
.ipl-banner,
.ipl-banner--ipl,
.ipl-banner--cpl {
  background: linear-gradient(30deg, #4a4f54 0%, #9e9e9e 100%);
  display: flex;
  margin: 0;
  padding: 0;
  position: relative;
  z-index: 0;
}

.ipl-banner--ipl {
  background: linear-gradient(30deg, #da5600 0%, #ff9015 100%);
}

.ipl-banner--cpl {
  background: linear-gradient(30deg, #007eb0 0%, #66c4e8 100%);
}

.ipl-banner--curve {
  bottom: -2px;
  height: 250px;
  overflow: hidden;
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 5000;
}
@media (min-width: 90em) {
  .ipl-banner--curve {
    height: 325px;
  }
}
.ipl-banner--curve::after {
  content: "";
  width: 300%;
  height: 0;
  padding-top: 200%;
  border-radius: 100%;
  background: #fff;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 48em) {
  .ipl-banner--curve::after {
    top: 65%;
    width: 330%;
  }
}
@media (min-width: 64em) {
  .ipl-banner--curve::after {
    top: 60%;
    width: 320%;
  }
}
@media (min-width: 90em) {
  .ipl-banner--curve::after {
    top: 70%;
    width: 370%;
  }
}

.ipl-banner--client {
  right: -5%;
  margin-right: auto;
  margin-left: auto;
  opacity: 0.25;
  position: relative;
  top: 2rem;
  z-index: 0;
}
@media (min-width: 30em) {
  .ipl-banner--client {
    left: -5rem;
    top: 1rem;
  }
}
@media (min-width: 48em) {
  .ipl-banner--client {
    right: 10%;
    top: 0;
  }
}
@media (min-width: 64em) {
  .ipl-banner--client {
    opacity: 0.5;
  }
}
.ipl-banner--client:after {
  background-image: url("data:image/svg+xml;base64,");
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  height: 700px;
  position: absolute;
  width: 710px;
}

.ipl-banner {
  align-items: center;
  display: flex;
  padding-bottom: 0.5rem;
  padding-top: 2rem;
  position: relative;
}

.ipl-banner__overlay {
  background-color: black;
  opacity: 0.3;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
}

.ipl-banner__content {
  align-content: center;
  display: flex;
  padding: 1rem;
  margin-top: 6rem;
  position: relative;
  z-index: 4000;
}

.ipl-banner__description {
  margin: 0;
}
@media (min-width: 35em) {
  .ipl-banner__description {
    margin: 0 10%;
  }
}
@media (min-width: 48em) {
  .ipl-banner__description {
    margin: 0 0 0 5vw;
  }
}
@media (min-width: 78em) {
  .ipl-banner__description {
    margin: 10vh 0 0 5vw;
  }
}

.ipl-tagline,
.ipl-tagline--white {
  align-items: flex-start;
  color: #212121;
  display: flex;
  flex-direction: column;
  margin: 0 0 1.25rem;
}
.ipl-tagline--white,
.ipl-tagline--white--white {
  color: white;
}

.ipl-banner__subtitle {
  margin-bottom: 0;
}

.ipl-banner__synopsis {
  color: white;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 2rem;
}
