  .ipl-banner__synopsis {
    font-size: 1.125rem;
    max-width: 45ch;
  }
}

.ipl-banner__visual--wrapper {
  bottom: 2.25rem;
  display: flex;
  left: 1rem;
  position: relative;
}
@media (min-width: 48em) {
  .ipl-banner__visual--wrapper {
    bottom: 1.75rem;
    left: auto;
  }
}

.ipl-banner__visual {
  bottom: 0;
  left: 0;
  margin: 1.25rem auto;
  max-width: 65%;
  position: relative;
  right: 0;
  top: 0;
}
@media (min-width: 48em) {
  .ipl-banner__visual {
    margin: auto;
    max-width: 75%;
  }
}
@media (min-width: 90em) {
  .ipl-banner__visual {
    max-width: 100%;
  }
}

