/* ==========================================================================
   #Skip link
   ========================================================================== */
/**
 * The skip (navigation) link provides a way for users of Assistive technology
 * to bypass large or repetitive blocks of content.
 *
 */
.skip-link {
  background-color: white;
  color: #4a4f54;
  font-size: rfs(18px);
  font-family: "Ubuntu", "Tahoma", sans-serif;
  font-weight: 400;
  padding: 0.75rem;
  position: absolute;
  left: -100%;
  text-decoration: none;
  top: -100%;
  width: auto;
  padding: 0.5rem;
  z-index: 10000;
}
.skip-link:active, .skip-link:focus-visible {
  left: 0;
  margin: 0.5rem;
  top: 0;
}

.skip-link__desktop {
  display: none;
}
@media (min-width: 48em) {
  .skip-link__desktop {
    display: block;
  }
}

.skip-link:not([tabindex="-1"]) {
  outline: 0.2rem solid transparent;
  outline-offset: 1rem;
}

/**
 * Utility classes are high-specificity, very explicit selectors 
