
/* ========================================================================
   #Gradients
   ======================================================================== */

/**
*
* To use the gradient background colour function, pass in colour preferences 
* the gradient is set 30deg though can be changed on :57 
*
* .example {
*   @include gradient(blue);
*  }
*
*  Will render as:
*
* .example {
*   background: linear-gradient(30deg, colour(blue-300), colour(blue-100));
*  }
*
*/

// Variables.

// Gradients map
$gradients : (
  blue: (
    start: colour(blue-300), 
    end:colour(blue-100)
  ),
  orange: (
    start: colour(orange-300),
    end: colour(orange-100)
  ),
  green: (
    start: colour(green-200), 
    end: colour(yellow-200)
  ),
  neutral: (
   start: colour(neutral-300), 
   end: colour(grey-500)
 ),
);

// Mixins.

// Function to pull colours from $gradients variable map.
// To pull a single colour outside of the gradient mixin:
// eg. background-color: grads(blue, start);
@function grads($colour, $position: start) {
  $list: map-get(map-get($gradients, $colour), $position);
  @return $list;
}

// Define a mixin for using gradients.
@mixin gradient($list) {
  background: linear-gradient(30deg, grads($list, start) 0%, grads($list, end) 100%);
}




