/* ==========================================================================
   #Headings
   ========================================================================== */

 /**
  * Default and opinionated styles for headings 1 to 6. 
  * 
  * Reference: 
  * https://www.smashingmagazine.com/2013/05/typographic-design-patterns-practices-case-study-2013/#average-font-size-for-headlines
  *
  * 
  * All heading font sizes, standard and opinionated are defined here. Passing 
  * these pixel values into the `ipl-font-size()` mixin will generate a rem-based 
  * `font-size` with a pixel fallback, as well as generating a `line-height` that  
  * will sit on the baseline grid.
  *
  */



// Default styles
  h1 {
    @include rfs(2.25rem);//36px
  }

  h2 {
    @include rfs(1.875rem);//30px
  }

  h3 {
    @include rfs(1.5rem);//24px
  }

  h4 {
    @include rfs(1.25rem);//20px
  }

  h5 {
    @include rfs(1rem);//16px
  }

  h6 {
    @include rfs(.875rem);//14px
  }


// Opinionated styles

 .ipl-h1 {
  @include rfs(3rem);//48px
  @include font(primary, bold);
 }

.ipl-h2 {
  @include rfs(2.625rem);//42px
  @include font(primary, bold);
}

.ipl-h3 {
  @include rfs(1.5rem);//24px
  @include font(primary, bold);
}

.ipl-h4{
  @include rfs(1.125rem);//18px
  @include font(primary, medium);
}

.ipl-h5 {
  @include rfs(1.0rem);//16px
  @include font(primary, regular);
}

.ipl-h6 {
  @include rfs(.875rem);//14px
  @include font(primary, regular);
}





