/* ========================================================================
  #Sprites
  ======================================================================== */
 
/**
  * Sprites rendered through the Mustache template
  *
  *  _./templates/sprite_template.scss
  *
  * embed within _./components/icons.scss
  *
  * or use as a background image:
  *
  * .example {
  *   @extend .my-icon;
  * }
  *
  */

 // Configuration
//  @use 'sass:math' as math;

  .ipl-icon {
    display: inline-flex;
    width: 1em;
    height: 1em;
    fill: currentColor;
  }
  
  .icons--alert {
    font-size:calc(35/10)*1rem;
    width:calc(36/35)*1em;
  }
  .icons--chevron-down {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .icons--arrow-left {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .icons--arrow-right {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .icons--arrow-up {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .icons--back-to-top {
    font-size:calc(17/10)*1rem;
    width:calc(16/17)*1em;
  }
  .icons--printer {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .icons--search {
    font-size:calc(24/10)*1rem;
    width:calc(24/24)*1em;
  }
  .ipl--logo {
    font-size:calc(148/10)*1rem;
    width:calc(631/148)*1em;
  }
  .ipl--logo-vertical {
    font-size:calc(125/10)*1rem;
    width:calc(130/125)*1em;
  }

