
/* ========================================================================
   #Horizontal-rules
   ======================================================================== */

/**
 * Horizontal rules to extend HR - further settings at 
 * _./components/divider.scss
 * 
 * Reference: https://css-tricks.com/simple-styles-for-horizontal-rules/
 *
 * <hr class="example">
 *
 * .example {
 *    @include horizontal-rule(color(neutral-200), dotted, 3px); 
 *  }
 *
 */ 

 // Configuration

@mixin horizontal-rule ($color, $style, $width) {
   color: $color;
   border: none;
   border-bottom-style: $style;
   border-bottom-width: $width;
   margin-top: calc(($ipl-line-height / 1em) / ($ipl-font-size / 1em)) * spacing(xs);
   margin-bottom: calc($ipl-line-height / $ipl-font-size) * spacing(xs);
 } 



