/* ==========================================================================
   #Back to top
   ========================================================================== */

/**
 * The Back to top button is used for long pages where users need to return to 
 * top for navigation, access filtering and sorting features or edit/enter
 * search box functions.
 *  
 * The back to top link is set to display: none until the page is longer than
 * 4 screens in height. If required edit the height value in ./js/scripts.js
 *
 * Reference: https://www.nngroup.com/articles/back-to-top/
 *
 */

.back-to-top {
  background-color: colour(blue-200);
  border: none;
  border-radius: 50%;
  bottom: spacing(-xl);
  box-shadow: 0 2px 3px 0 rgb(0 0 0 / 45%);
  color: colour(white);
  cursor: pointer;
  height: 1rem;
  line-height: 1.5;
  margin: 0;
  opacity: 0;
  padding: spacing(lg);
  position: fixed;
  right: spacing(sm);
  transition: all .25s ease-in-out;
  width: 1rem;
  z-index: z(overlay);

  &::before {
    bottom: 0;
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23FFFFFF' d='M12 5.5c.4 0 .9.2 1.2.5l10.2 10.2c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0l-9-9.1-9.1 9.1c-.7.7-1.7.7-2.4 0s-.7-1.7 0-2.4L10.8 6c.4-.3.8-.5 1.2-.5z'/%3E%3C/svg%3E");
    height: spacing(md);
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    width: spacing(md);
  }

  &:focus-visible::before {
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23212121' d='M12 5.5c.4 0 .9.2 1.2.5l10.2 10.2c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0l-9-9.1-9.1 9.1c-.7.7-1.7.7-2.4 0s-.7-1.7 0-2.4L10.8 6c.4-.3.8-.5 1.2-.5z'/%3E%3C/svg%3E");
  }

  &:hover:not(:focus)  {
    background-color: colour(blue-300);
  }
  &:focus-visible {
    outline: 0;
  }
}



//Active state
.back-to-top.active {
  display: flex;
  bottom: spacing(xl);
  opacity: 1;
  width: auto;

  @include mq($from: tablet) {
    bottom: spacing(xxxl);
  }

  &:focus:not(:hover) {
    border-color: colour(link-visited);
    border-width: 2px;
    outline: 3px solid colour(focus);
    outline-offset: 0;
  }

}

