/* ========================================================================
   #Links
   ======================================================================== */

/*
 * The link mixin generates states for visited and hover, focus has an 
 * individual state for outlines.
 *
 *  To adjust colours: ./elements/link-colours.scss
 */

 //Links
@mixin link-style($link, $visited, $hover) {
    color: $link;
    line-height: 1.67;   
    text-decoration: underline;
    text-underline-offset: .2em;
    text-decoration-thickness: 1px;

    &:visited {
        color: $visited;
    } 
    &:hover {
        color: $hover;
        text-decoration-thickness: 2px;
        -webkit-text-decoration-skip-ink: none;
        text-decoration-skip-ink: none;
        -webkit-text-decoration-skip: none;
        text-decoration-skip: none;
    } 
}


//Focus state
@mixin focus-visible($color) {
    &:focus:not(:focus-visible) {
      outline: none;
    }
    &:focus-visible {
        outline: 3px solid rgba(0,0,0,0);
        color: #0b0c0c;
        background-color: colour(focus);
        border-color: transparent;
        box-shadow: 0 -2px colour(focus),0 4px #0b0c0c;
        text-decoration: none;
        -webkit-box-decoration-break: clone;
        box-decoration-break: clone;
    }
  }

