/* ==========================================================================
   #flex-basiss
   ========================================================================== */

/**
 * Sizes in human readable format. These are used in conjunction with Sass 
 * media queries via ./tools/media-queries.scss.
 *
 * Also working in unison with ./objects/flexbox.scss
 */


/**
 * Whole
 */
.ipl-grid--one-whole {
  flex-basis: 100%;
}

/**
 * Halves
 */
.ipl-grid--one-half, 
.ipl-grid--two-quarters, 
.ipl-grid--three-sixths, 
.ipl-grid--four-eighths, 
.ipl-grid--five-tenths, 
.ipl-grid--six-twelfths {
  flex-basis: 100%;
  
  @include mq($from: tablet) {
    flex-basis: 50%;
  }

}

/**
 * Thirds
 */
.ipl-grid--one-third, 
.ipl-grid--two-sixths, 
.ipl-grid--four-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 33.333%;
  }

}

.ipl-grid--two-thirds, 
.ipl-grid--four-sixths, 
.ipl-grid--eight-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 66.666%;
  }

}

/**
 * Quarters
 */
.ipl-grid--one-quarter, 
.ipl-grid--two-eighths, 
.ipl-grid--three-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 25%;
  }
}

.ipl-grid--three-quarters, 
.ipl-grid--six-eighths, 
.ipl-grid--nine-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 75%;
  }
}

/**
 * Fifths
 */
.ipl-grid--one-fifth, 
.ipl-grid--two-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 20%;
  }
}

.ipl-grid--two-fifths, 
.ipl-grid--four-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 40%;
  }
}

.ipl-grid--three-fifths, 
.ipl-grid--six-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 60%;
  }
}

.ipl-grid--four-fifths, 
.ipl-grid--eight-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 80%;
  }
}

/**
 * Sixths
 */
.ipl-grid--one-sixth, 
.ipl-grid--two-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 16.666%;
  }
}

.ipl-grid--five-sixths, .ipl-grid--ten-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 83.333%;
  }
}

/**
 * Eighths
 */
.ipl-grid--one-eighth {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 8.333%;
  }
}

.ipl-grid--three-eighths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 37.5%;
  }
}

.ipl-grid--five-eighths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 62.5%;
  }
}

.ipl-grid--seven-eighths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 87.5%;
  }

}

/**
 * Tenths
 */
.ipl-grid--one-tenth {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 10%;
  }

}

.ipl-grid--three-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 30%;
  };
}

.ipl-grid--seven-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 70%;
  }
}

.ipl-grid--nine-tenths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 90%;
  }
}

/**
 * Twelfths
 */
.ipl-grid--one-twelfth {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 8.333%;
  }
}

.ipl-grid--five-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 41.666%;
  }
}

.ipl-grid--seven-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 58.333%;
  }
}

.ipl-grid--eleven-twelfths {
  flex-basis: 100%;

  @include mq($from: tablet) {
    flex-basis: 91.666%;
  }
}
