/* ==========================================================================
   #Media
   ========================================================================== */

/*
 * Place any image- and text-like content side-by-side utilizing 
 * `align-items: center;` to give control over the vertical alignments of the
 * text and image.
 *
 *
 * 1. Ability to control vertical alignments.
 * 2. Force the object to be the full width of its parent. Combined with [1],
 *    this makes the object behave in a quasi-`display: block;` manner.
 * 3. Reset inherited `border-spacing` declarations.
 * 4. Default to aligning content to their middles.
 */

 .ipl-media {
  align-items: center; /* [4] */
  border-spacing: 0; /* [3] */
  display: flex; /* [1] */
  width: 100%; /* [2] */
}


/**
 *  Add border radius and or box-shadow effects
 *
 */

 .ipl-media__shadow > img {
  box-shadow: 0 1px 10px -2px rgb(1 1 1 / 30%);
}

.ipl-media__border-radius > img {
  border-radius: spacing(md);
}

   
/**
 * Media images have a space between them and the body of the object.
 *
 */
 
 .ipl-media__image {
    padding-right: spacing(md);
    padding-bottom: spacing(xs);
 
     /**
      *    The direct child selector '>' needs to remain in order for nested media
      *    object to not inherit their parents formatting. In case the image tag
      *    is wrapped into another tag, e.g. an anchor for linking reasons, it will
      *    disappear. 
      *
      *    i.e.:
      *
      *      <a href="/">
      *        <div class="ipl-media__img">
      *          <img src="./link/to/image.jpg" alt="image alt text">
      *        </div>
      *      </a>
      */
 
      > img {
          display: block;
          max-width: 100%;
        }
 
   }
 
/**
 * The container for the main content of the media object.
 *
 * 1. Forces the `.ipl-media__body` to take up all remaining space.
 */
 
.ipl-media__body {
  flex: 1;

  p {
    margin: 0 0 spacing(sm) 0;
    max-width: max-content;
  }

}
 
 


// Gap size variants between image and content

//Flush
 .ipl-media--flush {
 
   > .ipl-media__image {
     padding-right: 0;
     padding-left:  0;
   }
 
 }
 
//Small 
 .ipl-media--small {
 
   > .ipl-media__image {
     padding-right: spacing(xs);
   }
 
   &.ipl-media--reverse {
      padding-right: 0;
      padding-left: spacing(xs);
 
     > .ipl-media__image {
       padding-right: 0;
       padding-left: spacing(xs);
     }
 
   }
 
 }
 
 //Medium
 .ipl-media--medium {
 
   > .ipl-media__image {
     padding-right: spacing(sm);
   }
 
   &.ipl-media--reverse {
 
     > .ipl-media__image {
       padding-right: 0;
       padding-left: spacing(sm);
     }
 
   }
 
 }
 
 //Large
 .ipl-media--large {
 
   > .ipl-media__image {
     padding-right: spacing(lg);
   }
 
   &.ipl-media--reverse {
 
     > .ipl-media__image {
       padding-right: 0;
       padding-left: spacing(lg);
     }
 
   }
 
 }
 
 //Extra large
 .ipl-media--x-large {
 
   > .ipl-media__image {
     padding-right: spacing(lg);
   }
 
   &.ipl-media--reverse {
 
     > .ipl-media__image {
       padding-right: 0;
       padding-left: spacing(lg);
     }
 
   }
 
 }
 

 // Reversed flag

 
 /**
  * 1. Swap the rendered direction of the object
  * 2. and reset it.
  * 3. Reassign margins to the correct sides.
  */
 
 .ipl-media--reverse {
  flex-direction: row-reverse; /* [1] */

   .ipl-media__image,
   .ipl-media__body {
     display: flex; /* [3] */
     flex-direction: row-reverse; /* [2] */
     padding-right: 0;
     padding-left: spacing(md); /* [3] */
   }

 }
 
 
 
 
 // Alignment variants
 
 /**
  * Vertically align the image- and body-content differently. Defaults to middle.
  */
 
 .ipl-media--top {
        align-items: flex-start;
   }
 
 
 .ipl-media--bottom {
       align-items: flex-end;
   }

 
 
// Wrap around image

/**
 *  Wrap text around the image - apply class to <wrapper> 
 *  e.g. <div class="ipl-media__wrap--left">
 *          <img src="" alt="">
 *       </div>
 */

 .ipl-media__wrap--left {
    float: left;
    margin-right: 0;
 } 
 
 .ipl-media__wrap--right {
    float: right;
    margin-left: 0;
 }




 // Desktop only 
 .ipl-media__desktop {
   display: none;

   @include mq($from: tablet) {
    display: flex;
 }

 }