/* ==========================================================================
   #Phrasing
   ========================================================================== */

/**
  * Phrasing-level elements, abbreviations & acronyms.
  * Visual indication of available title attributes.
  *
  */


// abbr - change value to adjust width between letters  
abbr {
    @include font-spacing(50);
  
    &[title] {
      cursor: help;
      border-bottom: 1px dotted;
    }
  }
  


  
  // Highlighted text.
  //
  // Add a small amount of padding to give the text some room within
  // the background color.
  mark {
    padding: 0.2em;
  }
  

  // Small print.
  small {
    @include rfs(.75rem);
    font-weight: normal;
  }


  // Code highlight
  //
  // Add background color to emphasise code
  .highlight {
    background: colour(code-view);
    color: #f4f4ec;
    font-size: 90%;
    border-radius: var(--radius);
    margin: 0;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    overflow-wrap: initial;
    padding: spacing(xs);
    display: inline;
}




/**
 * Keyboard strokes.
 * Code snippets and code blocks.
 */
 kbd,
 code,
 samp {
   background-color: color(code-view);
   border-radius: spacing(xs);
   color:color(code-class);
   display: inline-block;
   @include font(mono, medium);
   margin: spacing(xs);
   padding: spacing(xs);
 }

 code {
  white-space: pre;
  word-break: break-all;
}

 pre {
   @include font(mono, medium);
 }

 pre code {
   display: block;
   padding: 0;
   tab-size: 4;
 }