/* ==========================================================================
   #Flexbox grid
   ========================================================================== */

/*
 * Flexbox layout system. Support is for IE11+ 
 *
 * Cells are full-width and will stack as rows by default, media-queries are 
 * based off ./tools/media-queries.scss and ./objects/widths.
 *
 * <div class="ipl-flex">
 *   <div class="ipl-flex__cell">
 *   </div>
 *   <div class="ipl-flex__cell">
 *   </div>
 * </div>
 *
 * Cells will in most cases be accompanied by utility classes that divide
 * the grid into fractions:
 *
 * <div class="ipl-grid">
 *   <div class="ipl-grid__cell ipl-grid-one-third">
 *   </div>
 *   <div class="ipl-grid__cell ipl-grid-two-thirds">
 *   </div>
 * </div>
 *
 * Cells can be spaced equally using ipl-grid--auto without the need for 
 * utility classes:
 *
 *  <div class="ipl-grid ipl-grid--auto">
 *   <div class="ipl-grid__cell">
 *     50%
 *   </div>
 *  <div class="ipl-grid__cell">
 *    50%
 *  </div>
 * </div>
 *
 * Order of cells can be changed, use with caution when ordering content
 * in a meaningful sequence:
 *
 *  <div class="ipl-grid ipl-grid--auto">
 *   <div class="ipl-grid__cell ipl-order-2">
 *     50%
 *   </div>
 *  <div class="ipl-grid__cell ipl-order-1">
 *    50%
 *  </div>
 * </div>
 *
 */
 @import "../objects/widths";

 
 /* Gutter sizes
   ========================================================================== */

// Specify a map of possible gutter widths. By default use the global
// spacing units from core.scss and their conventional class
// name suffixes (--small, --medium, --large etc).



$ipl-flexbox-spacing-sizes: (
    "-02"    :   $spacing-01,
    "-04"    :   $spacing-02,
    "-08"    :   $spacing-03,
    "-12"    :   $spacing-04,
    "-16"    :   $spacing-05,
    "-24"    :   $spacing-06,
    "-32"    :   $spacing-07,
    "-48"    :   $spacing-08, 
    "-64"    :   $spacing-09,
    "-72"    :   $spacing-10,
    "-0"     :   0
) !default;


// Specify possible alignment values

$ipl-flexbox-alignment-values: (
    '--top': 'flex-start',
    '--middle': 'center',
    '--bottom': 'flex-end',
    '--baseline': 'baseline',
    '--stretch': 'stretch',
) !default;

// Specify possible justify values
// If you want to omit some of the modifiers,
// or change their suffixes (--middle, --bottom etc.), 
// simply add to the $ipl-flexgrid-justify-values below

$ipl-flexbox-justify-values: (
    '--left': 'flex-start',
    '--center': 'center',
    '--right': 'flex-end',
    '--between': 'space-between',
    '--around': 'space-around',
) !default;


/* The Flexbox grid
   ========================================================================== */

.ipl-grid {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    padding: 0;
}

/**
 * 1. Cells are full-width and stack on top of each other by default.
 */

.ipl-grid__cell {
    flex: 0 1 auto;
    width: 100%; /* [1] */
}


/* Gutters
   ========================================================================== 
      A set of gutter widths are provided as modifier classes. For example, the 
   following block will generate a grid with "large" gutters and a grid with 
   no gutters at all:

    <div class="ipl-grid ipl-grid--large">
        <div class="ipl-grid__cell">
        </div>
    </div>

    <div class="ipl-grid ipl-grid--flush">
        <div class="ipl-grid__cell">
        </div>
    </div>

   Available gutter sizes:

    ipl-grid--x-small
    ipl-grid--small
    ipl-grid--medium
    ipl-grid--large
    ipl-grid--x-large
    ipl-grid--flush
    Without a modifier, the default gutter size is equal to $ipl-spacing (1rem).
   
*/



/**
 * 1. The map $ipl-flexgrid-spacing-sizes contains a `null` key which
 *    will be the default gutter size.
 * 2. Add a negative margin on one side only. That way you aren't required to use
 *    a wrapper with the same amount of spacing, though it's common to do so.
 * 3. Neutralize the left margin on `ipl-grid`.
 */


@each $size-namespace, $size in $ipl-flexbox-spacing-sizes {
    .ipl-grid#{$size-namespace} { /* [1] */
        margin-left: -$size; /* [2] */
        & > .ipl-grid__cell {
            padding-left: $size; /* [3] */
        }
    }
}

/* Automatically size cells by distributing them equally
   ========================================================================== */

   .ipl-grid--auto {
    & > .ipl-grid__cell {
        flex: 1 0 0;
    }
}


/* Horizontal alignment
   ========================================================================== 

   Use the following modifiers for horizontal alignment:

    - ipl-grid--left: 
       Align cells left. Uses justify-content: flex-start (default).

    - ipl-grid--center: 
      Horizontally center cells. Uses justify-content: center.

    - ipl-grid--right: 
      Align cells right. Uses justify-content: flex-end.

    - ipl-grid--between: 
      Distribute free space between the cells. Uses justify-content: space-between.

    - ipl-grid--around: 
      Distribute free space around the cells. Uses justify-content: space-around.
*/

   @each $namespace, $value in $ipl-flexbox-justify-values {
    .ipl-grid#{$namespace} {
        justify-content: #{$value};
    }
}


/* Pull behavior
   ========================================================================== 
   
   Pull cells to the left or to the right with the following modifier classes:

   -ipl-grid__cell--pull-left: 
    Sets margin-right: auto.

   -ipl-grid__cell--pull-right: 
    Sets margin-left: auto.
   */

.ipl-grid__cell--pull-left {
    margin-right: auto;
}

.ipl-grid__cell--pull-right {
    margin-left: auto;
}


/* Vertical alignment
   ========================================================================== 
   
   Use the following modifiers for vertical alignment.

   .ipl-grid--stretch: 
     Stretch cells to fit the container. Uses align-items: stretch (default).

   .ipl-grid--middle: 
    Vertically center cells. Uses align-items: center.

    .ipl-grid--bottom: 
     Align cells to the bottom. Uses align-items: flex-end.

    .ipl-grid--top: 
     Align cells to the top. Uses align-items: flex-start.

    .ipl-grid--baseline: 
     Position cells at the baseline of the container. Uses align-items: baseline.
   */

@each $namespace, $value in $ipl-flexbox-alignment-values {
    .ipl-grid#{$namespace} {
        align-items: #{$value};
    }
}

@each $namespace, $value in $ipl-flexbox-alignment-values {
    .ipl-grid__cell#{$namespace} {
        align-self: #{$value};
    }
}


/* Content distribution
   ========================================================================== 
   
  Change the direction cells are placed in the grid with the following classes:

  .ipl-grid--column: 
   Sets flex-direction: column. Cells will be laid out from top to bottom instead 
   of from left to right.

   .ipl-grid--column-reverse: 
   Sets flex-direction: column-reverse. Cells will be laid out from bottom to top.
   */

   

 .ipl-grid--column {
    flex-direction: column;
}

.ipl-grid--column-reverse {
    flex-direction: column-reverse;
}

.ipl-grid--reverse {
    flex-direction: row-reverse;
}


//Place cells in order
.ipl-order-first {order:1;}
.ipl-order-last  {order:13;}
.ipl-order-0     {order:0;}
.ipl-order-1     {order:1;}
.ipl-order-2     {order:2;}
.ipl-order-3     {order:3;}
.ipl-order-4     {order:4;}
.ipl-order-5     {order:5;}
.ipl-order-6     {order:6;}
.ipl-order-7     {order:7;}
.ipl-order-8     {order:8;}
.ipl-order-9     {order:9;}
.ipl-order-10    {order:10;}
.ipl-order-11    {order:11;}
.ipl-order-12    {order:12;}
.ipl-order-13    {order:13;}
